/* ----- Edits 05052020 ------------------- */

.form-list-bl__btn_grid {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
  > * {
    margin-top: 20px; } }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .item-btn-brif {
    min-width: 49.5%;
    max-width: 49.5%; }
  /* end media min width 640 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .item-btn-brif {
    min-width: 33.3%;
    max-width: 33.3%; }
  /* end media min width 1000 pixels */ }


/* max width 767 pixels */
@media only screen and (max-width: 767px) {
  .slider-nav.slider-nav_big-mobile {
    max-width: 300px;
    margin: 0 auto;
    .slick-next {
      right: -4px; }
    .slick-prev {
      left: -4px; }
    .slick-dots li {
      margin: 4px 6px; } }
  /* end media max width 767 pixels */ }

