/* Navigation */
$SL-nav-wh: 30px;
$SL-nav-arr-w: 8;
$SL-nav-arr-h: 12;
$SL-nav-arr-left: url(../img/icons/arrow-left-slider.svg);
$SL-nav-arr-right: url(../img/icons/arrow-right-slider.svg);

.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  z-index: 1;
  top: 50%;
  width: $SL-nav-wh;
  height: $SL-nav-wh;
  margin: -($SL-nav-wh/2) 0 0;
  background-color: transparent;
  padding: 0;
  border: none;
  outline: none;
  &:before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    margin-top: -6px;
    margin-left: -5px;
    border-top: 6px solid transparent;
    border-bottom: 6px solid transparent; }
  &:hover {
    background-color: transparent; } }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-prev {
  left: -15px;
  &:before {
    border-right: 14px solid #979ed5; } }

.slick-next {
  right: -15px;
  &:before {
    border-left: 14px solid #979ed5; } }


.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }


[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto; }

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px; }

.slick-slide {
  outline: none; }

.slider-nav {
  text-align: center; }

.slider-project-wrapp .slider-nav {
  margin-top: -30px; }

.slider-nav__inner {
  position: relative;
  display: inline-block;
  padding-left: 65px;
  padding-right: 65px;
  &:after, &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    background-color: #979ed5;
    background-image: linear-gradient(to top, #979ed5 0%, #2a2341 100%);
    height: 1px;
    margin-top: -1px;
    width: 25px; }
  &:before {
    left: 30px; }
  &:after {
    right: 30px; } }

.slider-nav_push {
  margin: 20px 0; }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-nav_vertical {
    .slider-nav__inner {
      padding-top: 65px;
      padding-bottom: 65px;
      padding-left: 0;
      padding-right: 0;
      &:before,
      &:after {
        left: 50%;
        width: 1px;
        height: 25px; }
      &:before {
        top: 30px;
        background-image: linear-gradient(to bottom, #979ed5 0%, #2a2341 100%); }
      &:after {
        bottom: 30px;
        top: auto;
        background-image: linear-gradient(to top, #979ed5 0%, #2a2341 100%); } }
    .slick-prev, .slick-next {
      left: 50%;
      margin-left: -15px;
      margin-top: 0;
      &:before {
        border-left: 6px solid transparent;
        border-right: 6px solid transparent; } }
    .slick-prev {
      top: -10px;
      bottom: auto;
      &:before {
        border-top: none;
        border-bottom: 14px solid #979ed5; } }
    .slick-next {
      bottom: -8px;
      top: auto;
      &:before {
        border-bottom: none;
        border-top: 14px solid #979ed5; } } }
  /* end media min width 1200 pixels */ }




