/* ----- Controls styles ----------------- */
.btn {
  position: relative;
  display: inline-block;
  vertical-align: middle;
  margin: 0;
  padding: $btn-padding;
  font: $btn-font;
  border: none;
  border-radius: $btn-radius;
  text-align: center;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  color: $btn-color;
  background-color: #c1288f;
  background-image: $grad-btn;
  box-shadow: 0 5px 25px 6px rgba(255, 255, 255, 0.15), 0 0 5px rgba(255, 255, 255, 0.05);
  letter-spacing: 0.4px;

  &:hover {
    text-decoration: none;
    color: $btn-color-hover;
    background-color: #6d238a;
    background-image: $grad-btn-hover; }

  &_second-st {
    background-color: #bf7fbf;
    background-image: linear-gradient(to bottom, #bf7fbf 0%, #683568 100%);
    border: none;

    &:hover {
      background-color: $btn-fill-second-hover-color;
      background-image: linear-gradient(to top, #bf7fbf 0%, #683568 100%); } }

  &_border {
    border: $btn-border;
    color: $cl-light;
    line-height: $btn-border-line-height;
    background: transparent;
    box-shadow: none;
    &:hover {
      color: $cl-light;
      background-color: #bf7fbf;
      line-height: 24px;
      border-top: none;
      border-bottom: none;
      background-image: linear-gradient(to right, #c1288f 0%, #6d238a 100%);
      border-left-color: #c1288f;
      border-right-color: #6d238a;
      box-shadow: none; }

    &.btn_second-st {
      border-color: #bf7fbf;
      color: #fff;

      &:hover {
        color: #fff;
        background-color: #683568;
        border-left-color: #bf7fbf;
        border-right-color: #683568;
        background-image: linear-gradient(to right, #bf7fbf 0%, #683568 100%); } } }

  &_min-width {
    min-width: $btn-width-min;

    &_second {
      min-width: 180px; } }

  &_max {
    width: 100%; }

  &_sm {
    padding: $btn-padding-sm;

    &.btn_min-width {
      min-width: $btn-width-min-sm; } }

  &_md {
    padding: $btn-padding-md;
    border-radius: 28px;
    font-size: 18px;

    &.btn_min-width {
      min-width: $btn-width-min-md; } }

  &_lg {
    padding: $btn-padding-lg;
    border-radius: 29px;

    &.btn_min-width {
      min-width: 290px; } }

  &_icon {
    padding-left: 30px;
    padding-right: 30px; }

  &__icon-text {
    display: inline-block;
    vertical-align: middle; }

  &__icon {
    display: inline-block;
    vertical-align: middle;
    font-style: normal;
    width: 13px;
    height: 9px;
    background: url(../img/icons/arrow-right.svg) center center no-repeat;
    background-size: 100% 100%;


    &_right {
      margin-left: 20px; }

    &_left {
      margin-right: 20px; } }

  &_shadow {
    box-shadow: 0 5px 54px 6px rgba(255, 255, 255, 0.5), 0 0 15px rgba(255, 255, 255, 0.2); }

  &_shadow-lg {
    box-shadow: 0 10px 96px 24px rgba(255, 255, 255, 0.3), 0 0 50px rgba(255, 255, 255, 0.2); }

  &_shadow-none {
    box-shadow: none; }

  &_md-txt {
    font-size: 18px;
    letter-spacing: 0.45px; }

  &_lg-txt {
    font-size: 24px;
    letter-spacing: 0.6px; } }

input.btn {
  padding-top: 0;
  padding-bottom: 0;
  height: $input-height;
  width: auto;
  &_max {
    width: 100%; }
  &.btn_md {
    height: 56px; }

  &.btn_input-st {
    height: 39px;
    box-shadow: 0 5px 54px 6px rgba(255, 255, 255, 0.3), 0 0 15px rgba(255, 255, 255, 0.1);
    border-radius: 20px;
    border: none;
    font-size: 18px; } }

.form-group-grid__four-col {
  .btn {
    vertical-align: bottom;
    display: block; } }

.placeholder {
  color: $st-color-placeholder; }

input,
textarea {
  @include placeholder-color($st-color-placeholder); }

::-webkit-contacts-auto-fill-button {
  opacity: 0; }

input[type="password"]::-webkit-contacts-auto-fill-button {
  opacity: 1; }

input,
textarea {
  position: relative;
  height: $input-height;
  width: 100%;
  padding: $input-padding;
  border: $input-border;
  font: $input-font;
  color: $input-color;
  background-color: $input-fill-color;
  outline: none;
  border-radius: $input-radius;
  -webkit-appearance: none;
  box-shadow: $input-shadow;
  letter-spacing: 0.4px;

  &:hover {
    border-color: $input-hover-border-color; }

  &:focus {
    border-color: $input-focus-border-color; } }

input[type="radio"], input[type="checkbox"] {
  width: auto;
  margin: 3px 5px 3px 0;
  height: 12px; }

input[type="checkbox"] {
  -webkit-appearance: checkbox; }
input[type="radio"] {
  -webkit-appearance: radio; }

textarea {
  vertical-align: top;
  height: $textarea-height;
  padding: $textarea-padding;
  resize: none; }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  input,
  textarea {
    font-size: 16px;
    padding-left: 30px;
    padding-right: 30px; }
  textarea {
    height: 220px;
    padding-top: 20px;
    padding-bottom: 20px; }
  input {
    height: 64px; }
  /* end media min width 1000 pixels */ }


/* ----- END controls ------------------- */
