/* ----- table ------------------- */

//tabs
.tabs {
  margin-bottom: 20px;
  li {
    color: #aab8d5;
    font-size: 14px;
    line-height: 24px;
    letter-spacing: 0.5px;
    border-bottom: 1px solid $cl-border;
    padding: 10px 15px;
    cursor: pointer;
    &:hover {
      color: #fefefe; }
    &.current {
      color: #fefefe;
      font-size: 20px;
      font-weight: 600;
      cursor: default; } } }

.box_hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  clip: rect(0,0,0,0); }

//table

table {
  width: 100%;
  border-collapse: collapse; }

td {
  border-bottom: 1px solid $cl-border;
  padding: 20px 5px;
  font-size: 10px;
  line-height: 1.2; }

tr {
  &:last-child {
    td {
      border-bottom: none; } } }

.td-price {
  background-image: $grad1;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: $cl-light;
  font-size: 10px;
  font-weight: 700;
  line-height: 1.2;
  letter-spacing: 0.6px;
  min-width: 95px; }

.td-btn {
  .btn {
    padding: 5px 10px;
    font-size: 10px;
    line-height: 16px;
    &_border {
      &:hover {
        line-height: 18px; } } } }

.tabl_box {
  margin-left: -10px;
  margin-right: -10px;
  border-radius: 10px;
  background-color: #1b1431;
  background-image: $grad-card;
  padding: 5px 10px; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  td {
    font-size: 14px;
    line-height: 1.2; }

  .td-price {
    font-size: 14px;
    min-width: 132px; }

  .td-btn .btn {
    padding: 8px 20px;
    font-size: 14px; }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .td-price {
    font-size: 24px;
    line-height: 1.2;
    min-width: 200px; }

  td {
    font-size: 24px;
    padding: 20px 5px; }

  .tabl_box {
    padding: 5px 30px;
    margin: 0; }

  .td-btn .btn {
    font-size: 18px;
    line-height: 20px;
    padding: 9px 40px;
    &_border {
      &:hover {
        line-height: 22px; } } }

  //tabs
  .tabs {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    position: relative;
    z-index: 1;
    margin-bottom: 50px;
    li {
      line-height: 20px;
      padding: 25px 10px;
      font-size: 16px;
      &.current {
        font-size: 16px; } }
    &:after {
      content: ' ';
      position: absolute;
      bottom: -26px;
      right: 0;
      background-image: url(../img/sprite.png);
      background-repeat: no-repeat;
      width: 57px;
      height: 57px;
      background-position: -107px -226px; }

    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 0;
      height: 1px;
      right: 26px;
      background-color: $cl-border; }
    li {
      text-align: center;
      border-bottom: none; } }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  td {
    padding: 40px 20px; }

  .td-price {
    min-width: 240px; }

  .tabs li {
    padding: 25px 25px;
    font-size: 18px;
    &.current {
      font-size: 18px; }
    &:first-child {
      padding-left: 0; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .tabs li {
    padding: 25px 25px;
    font-size: 20px;
    line-height: 24px;
    &.current {
      font-size: 20px;
      font-size: 24px; }
    &:first-child {
      padding-left: 0; } }

  .section-tabs {
    margin-top: 80px; }
  /* end media min width 1200 pixels */ }

/* ----- END table --------------- */
