/*_________________F A N C Y_______________________*/

@import "standard/fancy";

.fancybox-close-small {
  background: transparent;
  border: 0;
  border-radius: 0;
  color: #e2bda1;
  cursor: pointer;
  height: 44px;
  margin: 0;
  padding: 6px;
  position: absolute;
  right: 0;
  top: 0;
  width: 44px;
  z-index: 10;

  svg {
    fill: transparent;
    opacity: .8;
    stroke: currentColor;
    stroke-width: 1.5;
    transition: stroke .1s; }

  &:focus {
    outline: none; }

  &:hover svg {
    opacity: 1; } }

.fancybox-slide--image .fancybox-close-small, .fancybox-slide--video .fancybox-close-small, .fancybox-slide--iframe .fancybox-close-small {
  color: #ccc;
  padding: 5px;
  right: -12px;
  top: -44px; }

.fancybox-slide--image .fancybox-close-small:hover svg, .fancybox-slide--video .fancybox-close-small:hover svg, .fancybox-slide--iframe .fancybox-close-small:hover svg {
  background: transparent;
  color: #e2bda1; }

.fancybox-is-scaling .fancybox-close-small, .fancybox-is-zoomable.fancybox-can-drag .fancybox-close-small {
  display: none; }

.st-modal {
  max-width: 1000px;
  width: 95%;
  margin: 0 auto;
  display: none;
  padding: 50px 10px 30px;
  position: relative;
  font-family: $site-font-family;
  font-weight: normal;
  &_dark {
    background-color: #292242;
    background-image: $grad-card; } }

.main-card-modal {
  max-width: 560px;
  padding: 0;
  box-shadow: 0 0 60px rgba(137, 136, 199, 0.2);
  border-radius: 10px;
  background-color: #292242;
  background-image: $grad-card;
  overflow: hidden; }

.main-card-modal__cap {
  height: 200px;
  overflow: hidden;
  background-position: center top;
  background-repeat: no-repeat;
  background-image: url(../img/modal-img1_3.jpg);
  background-size: cover;
  position: relative;
  img {
    position: absolute;
    top: 0;
    max-width: 100%;
    left: 50%;
    transform: translateX(-50%); } }

.main-card-modal__body {
  padding: 20px 15px;
  background-image: $grad-card; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .main-card-modal__body {
    padding: 24px 35px; }
  /* end media min width 480 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .fancybox-close-small {
    padding: 0;
    color: #e2bda1;
    width: 42px;
    height: 42px; }
  .st-modal {
    padding: 50px 40px 30px; }
  .main-card-modal {
    padding: 0; }
  /* end media min width 768 pixels */ }


// mainstyles
.fancybox-inner {
  .fancybox-toolbar {
    .fancybox-button.fancybox-button--zoom, .fancybox-button.fancybox-button--thumbs {
      display: none !important; } } }

/*_____________end F A N C Y_______________________*/
