/* ----- Portfolio page ------------------- */

.g-slider-slide {
  &:nth-child(n+2) {
    display: none; }
  &.slick-slide {
    display: block; } }

.g-slider {
  .slick-list {
    margin-left: -15px;
    margin-right: -15px; }
  .g-slider-slide {
    margin: 15px; } }

.custom-scrollbar {
  overflow: hidden; }

.g-list__item {
  margin-bottom: 20px;
  &:last-child {
    margin-bottom: 0; } }
.g-list__item.custom-scrollbar {
  max-height: 200px;
  height: 200px; }

.g-list__item_hidden-mobile, .g-list__item_hidden-tabl {
  display: none; }

.g-point {
  display: block; }

.g-slider-wrapp {
  margin-top: 50px; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .g-list__item.custom-scrollbar {
    max-height: 350px;
    height: 350px; }
  /* end media min width 480 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .g-list__item_hidden-mobile {
    display: block; }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .g-list__item_hidden-tabl {
    display: block; }

  .g-list__item.custom-scrollbar {
    max-height: 600px;
    height: auto; }

  .g-list__item {
    margin-bottom: 30px; }

  .g-slider-slide:nth-child(2n) {
    padding-top: 70px; }

  .g-slider-wrapp {
    .slider-nav {
      margin-top: 100px; } }
  /* end media min width 1000 pixels */ }

/* ----- END Portfolio page --------------- */
