/* ----- Review ------------------- */

.review-box {
  margin: 30px 0;
  p {
    color: $cl-txt2; }
  &-cap {
    position: relative;
    text-align: center;
    margin: 0 0 20px;
    &__border {
      display: inline-block;
      padding: 14px;
      position: relative;
      &-inner {
        &:before, &:after {
          content: ' ';
          position: absolute;
          width: 2px;
          height: 56px;
          border-radius: 1px;
          background-color: $cl-alto; }
        &:before {
          left: 0;
          top: 0; }
        &:after {
          bottom: 0;
          right: 0; } }
      &:before, &:after {
        content: ' ';
        position: absolute;
        width: 38px;
        height: 2px;
        border-radius: 1px;
        background-color: $cl-alto; }
      &:before {
        left: 0;
        top: 0; }
      &:after {
        bottom: 0;
        right: 0; } }
    &__img {
      width: 120px;
      height: 120px;
      position: relative;
      overflow: hidden;
      border-radius: 10px;
      > img {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translate(-50%, 0);
        min-width: 100%;
        min-height: 100%;
        width: 100%; } } } }

.p-card-box-tt_second {
  .p-card-box-tt__name-cap:before {
    background-position: -107px -226px; } }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .review-box {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    &-cap {
      flex: 0 0 300px;
      max-width: 300px;
      text-align: left;
      padding: 50px 0;
      margin-bottom: 0; }
    &__content {
      flex-grow: 1; } }
  .review-box-cap__img {
    width: 250px;
    height: 225px; }

  .review-slider-wrapp {
    position: relative;

    .slider-nav {
      position: absolute;
      bottom: 20px;
      left: 0;
      min-width: 300px; } }
  /* end media min width 768 pixels */ }



/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .review-slider-wrapp {
    padding-left: 68px;
    padding-right: 68px;

    .slider-nav {
      left: 68px; }

    .p-card-box-tt_left {
      margin-bottom: 37px; }
    .p-card-box-tt_right {
      margin-top: 42px; } }

  .review-box-cap {
    flex: 0 0 346px;
    max-width: 346px;
    padding-top: 40px;
    padding-bottom: 120px; }
  .review-slider-wrapp .slider-nav {
    bottom: 108px; }
  /* end media min width 1200 pixels */ }

/* ----- END Review --------------- */
