/* ----- Project ------------------- */

.slider-project-wrapp {
  margin-left: -15px;
  margin-right: -15px; }

.p-card-box {
  padding-left: 10px;
  padding-right: 10px;
  perspective: 1000px;
  overflow: hidden;
  margin-bottom: 50px;
  position: relative;
  z-index: 1;
  &-tt {
    position: relative;
    margin: 20px 0;
    &__name-cap {
      color: #b1bddc;
      font-size: 14px;
      font-weight: 600;
      line-height: 18px;
      letter-spacing: 0.45px;
      position: relative;
      padding-bottom: 5px;
      margin-bottom: 5px;
      border-bottom: 1px solid $cl-border;
      margin-right: 30px;
      padding-right: 30px;
      &:before {
        content: ' ';
        position: absolute;
        background-image: url(../img/sprite.png);
        background-repeat: no-repeat;
        width: 57px;
        height: 57px;
        background-position: -174px -226px;
        top: 100%;
        right: 0;
        margin-right: -28px;
        margin-top: -28px; } }

    &__name {
      color: $cl-light;
      font-size: 14px;
      font-weight: 600;
      line-height: 18px;
      letter-spacing: 0.5px;
      padding-right: 60px; }

    &_bottom, &_right {
      text-align: right;
      .p-card-box-tt__name-cap {
        padding-right: 0;
        margin-right: 0;
        margin-left: 30px;
        &:before {
          right: auto;
          left: 0;
          margin-left: -28px; } }

      .p-card-box-tt__name {
        padding-right: 0;
        padding-left: 60px; } } } }



.p-cards {
  display: flex;
  flex-wrap: wrap;
  position: relative;
  transition: transform ease-in-out 0.2s;
  transform-style: preserve-3d;
  margin-left: -1%;
  margin-right: -1%;
  z-index: 1;
  .p-card {
    flex: 0 0 48%;
    margin-left: 1%;
    margin-right: 1%; } }

.p-card {
  position: relative;
  transition: 0.3s;
  outline: none;
  margin-bottom: 10px;
  text-align: center;
  overflow: hidden;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  &_mob-hidden {
    overflow: hidden;
    display: none; }
  &:hover {
    cursor: pointer;
    transform: translateZ(10px); } }

.block-link {
  text-align: center;
  margin: 40px 0; }


/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .p-card {
    &_mob-hidden {
      display: block;
      overflow: visible; } }

  .p-cards {
    .p-card {
      flex: 0 0 31.3%; } }
  /* end media min width 480 pixels */ }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .p-card-box {
    &-tt {
      &__name-cap {
        font-size: 18px;
        line-height: 20px; }
      &__name {
        font-size: 20px;
        line-height: 24px; } } }

  .p-cards {
    // transform: rotateX(40deg) rotateZ(30deg) rotateY(-10deg)
    -moz-transform: rotateZ(20deg) rotateX(36deg) rotateY(-19deg) skew(-29deg, 1deg);
    -webkit-transform: rotateZ(20deg) rotateX(36deg) rotateY(-19deg) skew(-29deg, 1deg);
    -o-transform: rotateZ(20deg) rotateX(36deg) rotateY(-19deg) skew(-29deg, 1deg);
    -ms-transform: rotateZ(20deg) rotateX(36deg) rotateY(-19deg) skew(-29deg, 1deg);
    transform: rotateZ(20deg) rotateX(36deg) rotateY(-19deg) skew(-29deg, 1deg);

    display: block; }
  .p-cards .p-card {
    margin-left: auto;
    margin-right: auto; }
  .p-card {
    max-width: 200px;
    margin: 50px 0;
    box-shadow: 0 4px 20px rgba(21, 18, 33, 0.6);
    &:nth-child(1) {
      position: absolute;
      top: 10px;
      left: 200px; }
    &:nth-child(3) {
      position: absolute;
      top: -90px;
      right: 200px; } }

  .p-card-box-tt {
    &_top {
      max-width: 640px;
      min-width: 640px;
      max-width: 520px;
      min-width: 520px;
      position: absolute;
      top: 60px;
      left: 0; }
    &_bottom {
      position: absolute;
      right: 0;
      bottom: 30px;
      max-width: 550px;
      min-width: 550px; } }

  .slider-project-wrapp {
    margin-left: 0;
    margin-right: 0; }
  .p-card-box {
    padding-bottom: 0;
    margin: 20px 0; }

  .p-card-box-tt__name-cap {
    position: relative;
    z-index: 2; }


  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .p-card-box {
    max-width: 980px; }
  .p-card {
    max-width: 320px; }
  .p-card:nth-child(3) {
    top: -141px;
    right: 217px; }
  .p-card:nth-child(1) {
    left: 250px;
    top: 50px;
    max-width: 300px; }

  .p-card-box-tt_top {
    top: 98px;
    max-width: 680px;
    min-width: 680px;
    left: 15px; }

  .p-card-box-tt_bottom {
    bottom: 50px; }

  .p-card-box-tt__name-cap {
    padding-bottom: 14px;
    margin-bottom: 22px; }

  .p-card-box-tt_bottom {
    bottom: 23px;
    min-width: 580px;
    max-width: 580px; }

  .slider-project-wrapp {
    margin-left: -15px; }
  .p-card-box {
    padding-left: 25px; }

  .block-link {
    margin: 60px 0; }

  .section-st_bg-minion {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: -80px;
      right: 20px;
      background: url(../img/minion.png) center center no-repeat;
      background-size: contain;
      width: 84px;
      height: 113px; } }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-project-wrapp {
    position: relative;
    .slider-nav {
      position: absolute;
      top: 50%;
      transform: translateY(-40%);
      right: 47px; } }

  .section-st_bg-minion:after {
    width: 114px;
    height: 153px; }
  /* end media min width 1200 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .section-st_bg-minion {
    &:after {
      right: auto;
      left: 50%;
      margin-left: 520px;
      width: 134px;
      height: 183px; } }
  /* end media min width 1440 pixels */ }


