// colors
$cl-site: #2a2344;
$cl-site-second: #00C613;

$cl-txt: #bbc9e8;
$cl-txt2: #a9b7d4;
$cl-border: #787bc9;
$cl-border2: #1a2029;

$cl-light: #fff;
$cl-light-2: #ffffff;
$cl-dark: #000;
$cl-emperor: #555;
$cl-mineShaft: #232323;
$cl-Martinique: #292242;
$cl-green: #00BD12;
$cl-porsche: #eab15d;
$cl-blue: #1f30ff;
$cl-scarlet: #ff2c1a;
$cl-canary: #fdfd58;
$cl-silver: #ccc;
$cl-alto: #ddd;
$cl-gallery: #eee;
$cl-silverChalice: #aaa;
$cl-tacao: #efae7c;


$grad1: linear-gradient(-87deg, #e8e3e9 0%, #a17c7b 100%);
$grad2: linear-gradient(to top, #2f284b 0%, #7a789a 100%);
$grad3: linear-gradient(to top, #5e5192 0%, #c4c1f7 100%);
$grad-line1: linear-gradient(to left, #292242 0%, #a890cd 100%);
$grad-line2: linear-gradient(to right, #292242 0%, #a890cd 100%);
$grad-btn: linear-gradient(to bottom, #c1288f 0%, #6d238a 100%);
$grad-btn-hover: linear-gradient(to top, #c1288f 0%, #6d238a 100%);
$grad-card: linear-gradient(to top, #181426 0%, #383252 100%);

//scarlet | red || Canary | yellow

// Typography var
$site-font-style: normal;   //normal | italic | oblique | inherit
$site-font-variant: normal; //normal | small-caps | inherit
$site-font-weight: normal;
$site-fill-color: #2a2344;

$site-font-family: 'ProximaNova', Arial, sans-serif;
$site-font-family-2: 'AvenirNextCyr', Arial, sans-serif;
$site-font-family-3: 'Roboto', Arial, sans-serif;

$body-text-size: 12+(px)+/14+(px);
$site-text-size: 14+(px);
$site-line-height: 20+(px);

// $font-base: 'Roboto'
// $font-second: 'Roboto'

$site-text-color: #fff;

// Title site variables
$site-title-color: #fff;
$title-font-weight: $site-font-family-2;
$title-font-family: 700;
$title-line-height: 1.2+(em);

$site-h1-size-mobile: 30+(px);
$site-h2-size-mobile: 26+(px);
$site-h3-size-mobile: 24+(px);
$site-h4-size-mobile: 20+(px);
$site-h5-size-mobile: 18+(px);
$site-h6-size-mobile: 16+(px);

$site-h1-size: 40+(px);
$site-h2-size: 36+(px);
$site-h3-size: 26+(px);
$site-h4-size: 20+(px);
$site-h5-size: 18+(px);
$site-h6-size: 16+(px);


// Buttons variables
$btn-fill-color: $cl-site;
$btn-fill-hover-color: lighten($btn-fill-color, 5%);
$btn-fill-second-color: $cl-site-second;
$btn-fill-second-hover-color: lighten($btn-fill-second-color, 5%);
$btn-color: #fff;
$btn-color-hover: #fff;

$btn-radius: 20+(px);
$btn-font: normal normal 16+px+/+24+px $site-font-family;
$btn-padding: ((38-24)/2)+px 30px;
$btn-width-min: 130px;

//border btn
$btn-border: 1px solid #6d238a;
$btn-border-hover-color: $btn-fill-color;
$btn-border-line-height: 22px;
$btn-border-color-text: $btn-fill-color;
$btn-border-hover-color-text: #fff;

//sm
$btn-padding-sm: ((30-24)/2)+px 15px;
$btn-width-min-sm: 110px;
//md
$btn-padding-md: ((56-24)/2)+px 35px;
$btn-width-min-md: 230px;
//lg
$btn-padding-lg: ((58-24)/2)+px 30px;
$btn-width-min-lg: 200px;
//font-radius-sm
$btn-size-sm: 12px;
//font-radius-md
$btn-size-md: 16px;
//font-radius-lg
$btn-size-lg: 18px;
//font-family-bold
$btn-font-family-bold: $site-font-family;
$btn-font-weight-bold: 700;
//font-family-light
$btn-font-family-light: $site-font-family;
$btn-font-weight-light: 300;
//font-radius-sm
$btn-radius-sm: 5px;

// links variables
$color-link: #4a93df;
$color-h-link: darken($color-link, 5%);

// color .placeholder
$st-color-placeholder: #d7c6fb;

// Field Forms variables Input and Textarea
$input-height: 40+(px);
$input-padding: 0 15+(px);
$input-border: 1px solid #a890cd;
$input-font: normal 300 14px $site-font-family;
$input-color: #fff;
$input-fill-color: transparent;
$input-radius: 5px;
$input-shadow: none;

$input-hover-border-color: lighten(#a890cd, 10%);
$input-focus-border-color: lighten(#a890cd, 20%);

// Navigation
$burger-fill: #fff;
$burger-fill-open: $cl-light;

$font-menu-item-mobile: normal normal 20+(px)+/24+(px) $site-font-family-2;
$font-menu-item: normal normal 18+(px)+/20+(px) $site-font-family-2;
$fill-menu: $cl-site;
$fill-menu-hover: darken($cl-site, 5%);

// Field Forms textarea
$textarea-height: 130+(px);
$textarea-padding: 10+(px) 15+(px);

// breakpoint
$mob: 375px;
$mob-md: 480px;
$mob-lg: 640px;
$tabl: 768px;
$lap: 1000px;
$desk: 1200px;
$desk-md: 1600px;
$desk-lg: 1900px;

