/* ----- Cards ------------------- */

.main-cards {
  margin: 20px 0; }

.main-card {
  display: flex;
  flex-direction: column;
  position: relative;
  z-index: 1;
  outline: none;
  text-decoration: none;
  color: #fff;
  overflow: hidden;
  border-radius: 10px;
  margin-bottom: 20px;
  text-align: center;

  background: #373252;
  background: -moz-linear-gradient(top, #373252 0%, #363151 8%, #35304e 9%, #353050 12%, #342f4d 12%, #322d4b 23%, #322c48 24%, #312c4a 26%, #312b47 26%, #302b49 28%, #2e2844 35%, #2e2a45 38%, #2d2743 38%, #2c2843 40%, #2b2740 40%, #2b2742 42%, #2a263f 42%, #262138 57%, #241f36 57%, #241f36 62%, #211d34 64%, #1e1b30 76%, #1d1a2d 77%, #1e1930 77%, #1c192c 79%, #1d182e 81%, #1b182b 81%, #181425 100%);
  background: -webkit-linear-gradient(top, #373252 0%,#363151 8%,#35304e 9%,#353050 12%,#342f4d 12%,#322d4b 23%,#322c48 24%,#312c4a 26%,#312b47 26%,#302b49 28%,#2e2844 35%,#2e2a45 38%,#2d2743 38%,#2c2843 40%,#2b2740 40%,#2b2742 42%,#2a263f 42%,#262138 57%,#241f36 57%,#241f36 62%,#211d34 64%,#1e1b30 76%,#1d1a2d 77%,#1e1930 77%,#1c192c 79%,#1d182e 81%,#1b182b 81%,#181425 100%);
  background: linear-gradient(to bottom, #373252 0%,#363151 8%,#35304e 9%,#353050 12%,#342f4d 12%,#322d4b 23%,#322c48 24%,#312c4a 26%,#312b47 26%,#302b49 28%,#2e2844 35%,#2e2a45 38%,#2d2743 38%,#2c2843 40%,#2b2740 40%,#2b2742 42%,#2a263f 42%,#262138 57%,#241f36 57%,#241f36 62%,#211d34 64%,#1e1b30 76%,#1d1a2d 77%,#1e1930 77%,#1c192c 79%,#1d182e 81%,#1b182b 81%,#181425 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#373252', endColorstr='#181425',GradientType=0 );

  &:hover {
    text-decoration: none;
    color: #fff; }

  &__img {
    text-align: center;
    min-height: 220px;
    width: 100%;
    max-width: 100%;
    position: relative;
    flex-shrink: 0;
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      max-width: none;
      z-index: -1;
      max-width: 100%; } }


  &__content {
    padding: 10px 10px 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    flex-grow: 1; }
  &__tt {
    background-image: linear-gradient(to top, #8d3134 0%, #f2b9bd 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: $cl-light;
    font-size: 20px;
    font-weight: 700;
    line-height: 1.1;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    margin-bottom: 10px; }
  &__txt {
    color: $cl-light;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.45px;
    margin-bottom: 15px; }
  &__price {
    color: $cl-light;
    font-size: 16px;
    line-height: 20px;
    letter-spacing: 1px;
    margin-bottom: 10px;
    margin-top: auto;
    &-txt {
      background-image: $grad1;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      color: $cl-light;
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
      letter-spacing: 0.6px;
      margin-bottom: 15px; }
    b {
      background-image: $grad1;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
      background-clip: text;
      color: $cl-light;
      font-size: 18px;
      font-weight: 600;
      letter-spacing: 0.6px; } }

  &__price-next {
    color: $cl-light;
    margin-bottom: 15px;
    span {
      vertical-align: top;
      display: inline-block;
      margin-right: 10px;
      padding-top: 3px; } }

  &__push-down {
    margin-top: auto; } }




/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .main-cards {
    display: flex;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%;
    .main-card {
      flex: 0 0 48%;
      max-width: 48%;
      margin-left: 1%;
      margin-right: 1%; } }


  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .main-cards {
    margin-left: -0.5%;
    margin-right: -0.5%;
    .main-card {
      flex: 0 0 24%;
      max-width: 24%;
      margin-left: 0.5%;
      margin-right: 0.5%; } }

  .main-card {
    .btn {
      padding-left: 15px;
      padding-right: 15px;
      width: 100%; }
    .btn__icon_right {
      margin-left: 3px; } }

  .main-card__img {
    min-height: 150px; }

  .main-cards {
    margin: 60px 0; }
  /* end media min width 768 pixels */ }



/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .main-card {
    &:hover {
      box-shadow: 0 0 40px rgba(137, 136, 199, 0.3); }
    &__tt {
      font-size: 30px;
      line-height: 1.1;
      margin-bottom: 18px; }
    &__txt {
      font-size: 18px;
      line-height: 24px;
      margin-bottom: 25px; }
    &__price {
      font-size: 20px;
      line-height: 26px;
      margin-bottom: 6px;
      &-txt {
        font-size: 24px;
        line-height: 26px;
        margin-bottom: 25px; }
      b {
        font-size: 24px; } }

    &__price-next {
      margin-bottom: 20px; } }

  .main-card__img {
    min-height: 200px; }

  .main-cards {
    margin-top: 100px; }

  .main-card {
    .btn {
      padding-left: 30px;
      padding-right: 30px;
      width: 100%; }
    .btn__icon_right {
      margin-left: 20px; } }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .main-cards {
    margin-left: -1%;
    margin-right: -1%;
    .main-card {
      flex: 0 0 23%;
      max-width: 23%;
      margin-left: 1%;
      margin-right: 1%; } }

  .main-card__img {
    min-height: 330px;
    img {
      max-width: 160%;
      transform: translate(-50%, -48%); } }

  .main-card__tt {
    margin-bottom: 9px; }

  .main-card__txt {
    margin-bottom: 32px; }

  .main-card__content {
    padding: 10px 22px 50px; }
  /* end media min width 1200 pixels */ }

/* ----- END Cards --------------- */
