/* ----- Forms ------------------------- */
.st-form {
  margin-bottom: 20px;

  &__tt {
    display: block; } }


.form-group {
  margin-bottom: 13px;

  &__label {
    display: block;
    margin-bottom: 10px;
    font-size: 14px;
    line-height: 18px; }

  &_inline {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .form-group__label {
      min-width: 140px;
      max-width: 140px;
      padding: 0 10px 0 0;
      margin: 5px 0; } } }

.form-group_check {
  display: flex;
  justify-content: flex-start;
  .form-group__label {
    order: 1; } }

.form-group_btn {
  margin-top: 30px; }

.form-group__field-dot {
  position: absolute;
  bottom: -12px;
  left: -12px;
  width: 24px;
  height: 24px;
  background: url(../img/icons/pin-sm.svg) left center no-repeat;
  background-size: contain; }

.form-st_second-style {
  .form-group {
    margin-bottom: 20px; }
  input, textarea {
    border-left: none;
    border-top: none;
    border-right: none;
    border-bottom: 1px solid $cl-border;
    border-radius: 0px;
    height: 40px;
    padding-left: 15px;
    padding-right: 15px;
    &:hover, &:focus {
      border-bottom-color: lighten($cl-border, 10%); } }

  .form-group {
    position: relative;
    z-index: 1;
    &_btn {
      margin-top: 40px; } } }

.form-st_three-style {
  input, textarea {
    height: 40px;
    border-left: none;
    border-top: none;
    border-right: none;
    border-bottom: 1px solid $cl-border;
    border-radius: 0px;
    padding-left: 15px;
    padding-right: 15px;
    &:hover, &:focus {
      border-bottom-color: lighten($cl-border, 10%); } }
  .form-group {
    margin-bottom: 15px; } }

.form-st_banner {
  margin-left: -10px;
  margin-right: -10px;
  padding: 20px 15px;
  border-radius: 10px;
  background-color: #1b1431;
  background-image: linear-gradient(to top, #1c172e 0%, #383252 100%); }

.line-load {
  display: flex;
  justify-content: flex-start;
  align-items: flex-end;
  margin: 30px 0 20px; }

.line-load__bar {
  background-color: #645e7a;
  height: 2px;
  flex-grow: 1;
  position: relative;
  top: -8px;
  span {
    position: absolute;
    top: -1px;
    background-color: #645e7a;
    min-width: 25%;
    background-color: $cl-light;
    background-image: $grad1;
    bottom: -1px;
    left: 0; } }

.line-load__value {
  flex-shrink: 0;
  margin-left: 30px;

  background-image: $grad1;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  color: $cl-txt2;
  font-size: 40px;
  font-weight: 700;
  line-height: 1;
  letter-spacing: 1px; }

.form-list-bl__item {
  margin-bottom: 80px; }

.form-list-bl__btn {
  text-align: center; }

.form-group__bl {
  margin-bottom: 20px; }

.form-group__txt {
  color: #d7c6fb;
  font-size: 12px;
  font-weight: 300;
  line-height: 16px;
  letter-spacing: 0.4px;
  margin-bottom: 15px; }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .form-st_second-style {
    .form-group-grid {
      display: flex;
      justify-content: space-between;
      &__half-col {
        width: 45.5%; } } }
  /* end media min width 480 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .form-group-grid {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;

    &__half-col {
      width: 47.5%; }

    &__three-col {
      width: 31.3%; }

    &__four-col {
      width: 22.8%; } }

  .form-st_three-style {
    input, textarea {
      height: 62px; } }

  .line-load {
    margin-top: 50px;
    max-width: 550px; }

  .line-load__value {
    margin-left: 35px; }

  .form-st_banner {
    max-width: 830px;
    margin-left: auto;
    margin-right: auto;
    padding: 50px 45px; }

  .form-group__txt {
    font-size: 16px;
    line-height: 24px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .form-group {
    &_push-md {
      margin-bottom: 30px; }

    &__label {
      font-size: 18px;
      line-height: 22px; } }

  .form-group_btn {
    margin-top: 57px; }

  .f-contact-grid__info {
    flex: 0 0 49%;
    max-width: 49%;
    padding-right: 20px; }
  .f-contact-grid__form {
    max-width: 51%;
    flex: 0 0 51%;
    padding-top: 5px; }

  .f-contact-info {
    margin-bottom: 20px;
    padding-left: 60px; }

  .f-logo {
    margin-bottom: 70px; }

  .f-contact-bl__tt {
    margin-bottom: 14px; }

  .f-contact-bl__txt {
    line-height: 27px; }

  .f-contact-bl {
    margin-bottom: 58px; }

  .f-contact-point {
    margin-bottom: 18px; }

  .f-contact-points {
    margin: 55px 0; }

  .social.social_left {
    li {
      margin-right: 8px; } }

  .f-contact-info {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: 0;
      left: -15px;
      right: 0;
      height: 656px;
      background: url(../img/mask-grid.png) center center no-repeat;
      z-index: -1; } }


  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .f-contact-info:after {
    top: -49px;
    left: -54px; }

  .form-group_big {
    margin-top: 50px; }

  .line-load {
    margin-top: 80px; }

  .form-list-bl {
    margin-top: 100px; }

  .form-list-bl__item {
    margin-bottom: 100px; }
  .form-list-bl__btn {
    margin-top: 100px; }
  /* end media min width 1200 pixels */ }

/* ----- END Forms --------------------- */
