/* ----- Page structure ---------------- */
.clear {
  clear: both; }

.group:after {
  content: ".";
  clear: both;
  display: block;
  height: 0;
  visibility: hidden; }

#wrapper {
  min-width: 320px; }

.section-st {
  padding: 20px 0;
  &_md {
    padding: 50px 0; }
  &_bt-md {
    padding: 0 0 50px; }
  &_md-top {
    padding: 50px 0 20px; }
  &_lg-top {
    padding: 50px 0; }
  &_bt-lg {
    padding-bottom: 80px; }
  &_top-none {
    padding-top: 20px; }
  &_no-scroll {
    overflow: hidden; }
  &_bt-none {
    padding-bottom: 0; } }


.section-banner {
  padding-bottom: 20px; }

.container {
  padding-left: 15px;
  padding-right: 15px;
  width: auto; }

.visible-lap, .hidden-full, .hidden-mobile {
  display: none; }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .hidden-mobile {
    display: block; }

  .hidden-tabl {
    display: block; }

  .section-banner {
    padding-bottom: 70px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  html {
    height: 100%; }

  body {
    display: flex;
    flex-direction: column;
    min-height: 100%; }

  #wrapper {
    flex: 1 0 auto; }

  .footer {
    flex-shrink: 0; }

  .section-st {
    padding: 50px 0;
    &_md {
      padding: 110px 0; }
    &_bt-md {
      padding: 0 0 110px; }
    &_md-top {
      padding: 110px 0 50px; }
    &_lg {
      padding: 140px 0; }
    &_lg-top {
      padding: 140px 0 70px; }
    &_bt-lg {
      padding-bottom: 240px; }
    &_bt-none {
      padding-bottom: 0; }
    &_top-none {
      padding-top: 20px; } }

  .section-banner {
    padding-bottom: 90px; }

  .visible-lap {
    display: block; }

  .hidden-lap {
    display: none; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .hidden-lap {
    display: block; }
  .container {
    max-width: 1200px;
    margin: 0 auto;
    width: auto;
    padding-left: 15px;
    padding-right: 15px; }

  .header {
    .menu-item {
      transition: 0s; } }

  #wrapper.sticky {
    padding-top: 115px;
    .header {
      position: fixed;
      z-index: 200;
      top: 0;
      left: 0;
      right: 0;
      background: #282240 linear-gradient(to top, #181426 0%, #383252 100%);
      padding: 5px 0;
      animation: linear header-fix 0.3s both;
      .menu-item_middle.menu-item {
        padding-bottom: 0;
        padding: 5px; }
      .menu-item__logo {
        max-width: 140px;
        display: block; } } }

  @keyframes header-fix {
    0% {
      top: -115px; }
    100% {
      top: 0; } }
  /* end media min width 1200 pixels */ }

























