/* ----- Index page -------------------- */

.boxes-quote {
  margin-top: 60px; }
.box-quote {
  color: #b1bddc;
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.75px;
  margin: 30px 0 30px;
  text-align: center;
  p {
    display: inline-block;
    position: relative;
    z-index: 1;
    padding: 10px 10px;
    &:after, &:before {
      content: ' ';
      position: absolute;
      width: 56px;
      height: 38px;
      z-index: -1; }
    &:after {
      bottom: 0;
      left: 0;
      background: url(../img/border-angle2.png) center center no-repeat; }
    &:before {
      top: 0;
      right: 0;
      background: url(../img/border-angle1.png) center center no-repeat; } } }

.boxes-quote__img {
  text-align: center; }

.boxes-quote__img {
  text-align: center; }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .box-quote {
    font-size: 24px;
    line-height: 34px;
    p {
      padding: 30px 0; } }

  .box-quote p:before {
    right: 20px; }
  .box-quote p:after {
    left: 20px; }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .box-quote {
    font-size: 30px;
    line-height: 42px;
    margin: 50px 0; }
  .boxes-quote {
    margin-top: 120px; }

  .box-quote p:before {
    right: 10px; }
  .box-quote p:after {
    left: 40px; }
  /* end media min width 1000 pixels */ }


/* ----- END index page /--------------- */
