/* ----- Banner ------------------- */
.section-banner {
  position: relative; }

.banner {
  padding: 40px 0 0;
  &__content {
    margin-bottom: 25px;
    .title-site_h3, h3 {
      margin-bottom: 35px; }
    p {
      margin-bottom: 35px; } }

  &__img {
    position: relative;
    z-index: 1;
    padding: 40px;
    text-align: center;
    min-height: 300px;
    max-width: 290px;
    margin: 0 auto;
    &:after {
      content: ' ';
      position: absolute;
      top: 0;
      bottom: 0;
      right: 0;
      left: 0;
      background: url(../img/dots-banner.png) center top no-repeat;
      background-size: contain;
      z-index: -1; } } }

.banner-footer-line {
  background: url(../img/line-section.png) center top no-repeat;
  background-size: auto 100%; }

.social {
  li {
    display: inline-block;
    vertical-align: middle;
    a {
      display: inline-block;
      position: relative;
      top: 0;
      padding: 10px;
      transition: ease-in-out 0.15s;
      opacity: 1;
      &:hover {
        opacity: 0.7;
        top: -2px; } } } }

.banner-footer {
  text-align: center;
  padding: 20px 0 20px 50px; }

.banner-footer-skills {
  padding: 20px 0;
  position: relative;
  z-index: 1;
  &__item {
    background-image: $grad2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: $cl-light;
    font-family: $site-font-family-2;
    font-size: 20px;
    margin-bottom: 5px;
    font-weight: 700;
    line-height: 1.1;
    letter-spacing: 1.13px;
    text-align: left;
    margin-right: 75px;
    &_left {
      margin-bottom: 80px;
      margin-right: 130px; }
    &_right {
      margin-bottom: 10px; } }

  &__img {
    max-width: 150px;
    position: absolute;
    right: 0;
    top: 10px;
    bottom: 10px;
    z-index: -1;
    > img {
      max-height: 100%; } } }


//comets
.banner-comet {
  background-position: center center;
  background-repeat: no-repeat;
  &_one {
    width: 101px;
    height: 133px;
    background-image: url(../img/comet1.png); }
  &_two {
    width: 56px;
    height: 70px;
    background-image: url(../img/comet2.png); }
  &_three {
    width: 100px;
    height: 133px;
    background-image: url(../img/comet3.png); } }

.banner-comets {
  display: none; }

.banner__content {
  .bread-crumbs {
    margin-bottom: 30px;
    display: block; } }

.banner__img_center {
  img {
    max-height: 200px; } }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {

  .banner-footer-skills {
    &__item {
      margin-right: 130px;
      &_left {
        margin-bottom: 10px; }
      &_right {
        margin-bottom: 10px; } } }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .banner {
    &__content {
      .title-site_h3, h3 {
        line-height: 34px; } } }

  .banner__grid {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    .banner__content {
      flex-grow: 1; }
    .banner__img {
      flex: 0 0 300px;
      margin-left: 30px; } }

  .banner__img {
    max-width: 504px;
    min-height: 300px;
    padding: 30px 55px; }

  .banner-footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 35px 0;
    max-width: 738px;
    margin: 0 auto; }

  .banner {
    padding-bottom: 40px; }

  .banner-footer-skills {
    &__item {
      font-size: 25px;
      line-height: 32px;
      margin-right: 0;
      &_left {
        text-align: left; }
      &_right {
        text-align: right; }
      &_center {
        text-align: center; } } }

  .banner-footer-skills__img {
    right: auto;
    left: 50%;
    transform: translateX(-50%);
    top: -140px;
    bottom: 60px;
    max-width: 300px;
    width: 300px;
    text-align: center; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .banner__grid {
    .banner__img {
      flex: 0 0 450px;
      margin-left: 30px; } }
  .banner__img {
    min-height: 300px;
    padding: 30px 55px; }

  .banner-footer {
    padding: 45px 0 85px;
    max-width: 940px; }

  .banner-footer-skills__img {
    top: -190px;
    margin-left: -30px; }

  .banner-footer-skills {
    &__item {
      font-size: 35px;
      line-height: 1.1; } }
  .banner-footer-skills__item_right {
    margin-bottom: 50px; }

  .banner__img_center img {
    max-height: 320px; }

  .banner__content .bread-crumbs {
    margin-bottom: 60px;
    margin-top: 30px; }

  .section-banner {
    overflow-x: hidden; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .banner {
    padding: 56px 30px 10px 65px; }

  .banner__grid .banner__img {
    flex: 0 0 520px;
    margin-left: 50px; }

  .banner__img {
    padding: 52px 43px 22px 60px;
    min-height: 400px;
    &_center {
      padding-right: 102px;
      padding-top: 70px; } }

  .banner__grid {
    align-items: flex-start;
    .banner__content {
      padding-top: 31px; } }

  .banner__content .title-site_h3, .banner__content h3 {
    margin-bottom: 41px; }

  .banner__content p {
    margin-bottom: 41px; }

  .banner__content .btn {
    margin-top: 10px; }

  .banner-footer {
    padding: 45px 0 77px;
    max-width: 975px;
    padding-right: 7px; }

  .banner-footer-skills {
    padding-top: 0;
    padding-left: 44px;
    padding-right: 33px;
    &__img {
      margin-left: -110px; }
    &__item {
      font-size: 30px;
      &_left {
        font-size: 30px;
        margin-bottom: 2px; }
      &_right {
        font-size: 40px;
        margin-bottom: 90px; }
      &_center {
        font-size: 45px;
        margin-right: 140px; } } }

  /* end media min width 1200 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .banner-comets {
    display: block;
    .banner-comet {
      position: absolute;
      top: 0;
      left: 0;
      &_one {
        top: 10px;
        left: 10px; }
      &_two {
        top: 10px;
        left: 120px; }
      &_three {
        top: 240px;
        left: 100px; } } }
  /* end media min width 1440 pixels */ }


/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .banner-comets .banner-comet_one {
    top: 70px; }
  .banner-comets .banner-comet_two {
    top: 70px;
    left: 150px; }
  .banner-comets .banner-comet_three {
    top: 280px;
    left: 260px; }
  /* end media min width 1800 pixels */ }


/* Min width 2000 pixels */
@media only screen and (min-width: 2000px) {
  .banner-footer-line {
    background-size: 100% 57%; }
  /* end media min width 2000 pixels */ }

