/* ----- Team page ------------------- */
.team-slider-slide {
  margin: 15px;
  &:nth-child(n+2) {
    display: none; }
  &.slick-slide {
    display: block; } }

.team-slider {
  .slick-list {
    margin-left: -15px;
    margin-right: -15px; } }


.team-box {
  display: block;
  text-decoration: none;
  outline: none;
  position: relative;
  z-index: 1;
  margin-bottom: 40px;

  &_mob-hidden {
    display: none; }

  &-cap {
    position: relative;
    padding: 19px;
    display: block;
    text-align: center;
    margin-bottom: 20px;
    &:before, &:after {
      content: ' ';
      position: absolute;
      background-size: contain;
      width: 38px;
      height: 56px; }
    &:before {
      top: 0;
      left: 0;
      background: url(../img/border-angle3.png) left top no-repeat; }
    &:after {
      right: 0;
      bottom: 0;
      background: url(../img/border-angle4.png) right bottom no-repeat; }
    &__inner {
      position: relative;
      z-index: 1;
      height: 250px;
      width: 100%;
      overflow: hidden;
      border-radius: 10px;
      background-image: $grad-card;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: center top; } } } }

.team-box-cap__open {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  padding: 10px 20px;
  display: flex;
  justify-content: flex-end;
  align-items: flex-end;
  text-align: right;
  color: $cl-light;
  font-size: 12px;
  line-height: 1.2;
  letter-spacing: 0.5px;
  background-image: linear-gradient(to top, rgba(#181426, 0.5) 0%, rgba(#383252, 0.5) 100%);
  li, p {
    list-style: none;
    margin-bottom: 10px; } }

.team-box-info {
  text-align: right; }

.team-box-info__tt {
  color: $cl-light;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.1;
  letter-spacing: 0.54px;
  position: relative;
  padding-left: 58px;
  padding-bottom: 13px;
  margin-bottom: 13px;
  &:before {
    content: ' ';
    position: absolute;
    bottom: 0;
    left: 25px;
    right: 0;
    height: 1px;
    background-color: $cl-border; }
  &:after {
    content: ' ';
    position: absolute;
    bottom: -28px;
    left: 0;
    background-image: url(../img/sprite.png);
    background-repeat: no-repeat;
    width: 57px;
    height: 57px;
    background-position: -107px -226px; } }
.team-box-info__subtt {
  line-height: 1.2;
  color: #b1bddc;
  font-size: 18px;
  letter-spacing: 0.58px;
  padding-left: 58px; }

.img-update {
  margin-top: 100px;
  text-align: center; }

.contact-tt {
  color: $cl-light;
  font-size: 18px;
  font-weight: 600;
  line-height: 22px;
  letter-spacing: 0.6px;
  position: relative;
  padding-left: 60px;
  margin-bottom: 20px;
  &:before {
    content: ' ';
    position: absolute;
    top: 10px;
    left: 0;
    width: 28px;
    height: 3px;
    background-color: $cl-light;
    background-image: $grad1; } }

.contact-list {
  &__item {
    margin-bottom: 20px; } }

.contact-grid__item {
  margin-bottom: 30px; }

.txt-icon {
  padding-left: 40px;
  position: relative;
  z-index: 1;
  text-decoration: none;
  outline: none;
  display: inline-block;
  &__pic {
    position: absolute;
    top: 2px;
    left: 0; }
  &__tt {
    color: $cl-light;
    font-size: 18px;
    line-height: 22px;
    letter-spacing: 0.45px; }
  &__subtt {
    color: $cl-txt2;
    letter-spacing: 0.35px; } }

a.txt-icon {
  &:hover, &:focus {
    text-decoration: none;
    .txt-icon__tt {
      text-decoration: underline; } } }

/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .contact-grid {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -5px;
    margin-right: -5px;
    &__item {
      padding-left: 5px;
      padding-right: 5px;
      min-width: 50%;
      max-width: 50%; } }
  /* end media min width 480 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .team-box_mob-hidden {
    display: block; }

  .contact-tt {
    font-size: 24px;
    line-height: 30px;
    &:before {
      top: 14px; } }

  .contact-list {
    margin-left: 60px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .team-box-cap__open {
    font-size: 20px; }
  .team-box-info__tt {
    font-size: 22px;
    line-height: 1.1; }
  .team-box-info__subtt {
    line-height: 1.2;
    font-size: 24px; }
  .team-box-cap__inner {
    height: 390px;
    &:hover {
      .team-box-cap__open {
        top: 0; } } }

  .team-box-cap__open {
    top: 101%;
    bottom: -1px;
    overflow: hidden;
    transition: 0.2s; }

  .team-slider-wrap__nav {
    margin-top: 0px; }

  .img-update {
    margin-top: 150px; }

  .team-box {
    margin-bottom: 90px; }

  .contact-grid {
    margin-left: -15px;
    margin-right: -15px;
    justify-content: space-between;
    &__item {
      padding-left: 15px;
      padding-right: 15px;
      min-width: 20%;
      max-width: 32%; } }

  .contact-tt {
    margin-bottom: 34px; }
  .contact-list__item {
    margin-bottom: 25px; }
  .txt-icon__tt, .txt-icon__subtt {
    margin-bottom: 5px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .team-box-cap__open {
    padding: 10px 30px;
    li, p {
      margin-bottom: 20px; } }

  .contact-grid {
    position: relative;
    z-index: 1;
    padding-top: 100px;
    &:before {
      content: ' ';
      position: absolute;
      top: -46px;
      left: 50%;
      margin-left: -625px;
      width: 1261px;
      height: 576px;
      background: url(../img/grid-mask-contact.png) center top no-repeat;
      background-size: contain; } }
  /* end media min width 1200 pixels */ }


