/* DOTS */
$SL-dot-wh: 6px;
$SL-dot-margin: 8;
$wh-dots: $SL-dot-wh+($SL-dot-margin*2);

.slick-dots {
  text-align: center;
  position: relative;
  z-index: 1;
  line-height: 0px;

  li {
    padding: 0;
    position: relative;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    margin: 0 $SL-dot-margin+(px);

    button {
      font-size: 0;
      line-height: 0;
      padding: 0;
      margin: 0;
      box-shadow: none;
      outline: none;
      display: inline-block;
      position: relative;
      box-sizing: content-box;
      background-clip: padding-box;

      background-color: #979ed5;
      border-radius: 50%;
      width: $SL-dot-wh;
      height: $SL-dot-wh;
      border: 1px solid transparent;


      &:hover:before, &:focus:before {
        opacity: 1; }

      &:before {
        content: ' ';
        background-color: transparent;
        border-radius: 50%;
        position: absolute;
        top: 50%;
        left: 50%;
        margin-top: -($wh-dots/2)+(px);
        margin-left: -($wh-dots/2)+(px);
        width: $wh-dots+(px);
        height: $wh-dots+(px); } }

    &.slick-active button {
      border: none;
      width: 8px;
      height: 8px;
      background-color: #b27dc0;
      background-image: linear-gradient(to bottom, #bf7fbf 0%, #683568 100%);
      box-shadow: 0 0 10px 1px rgba(189, 178, 255, 0.43); } } }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .slider-nav_vertical {
    .slick-dots {
      li {
        display: block;
        margin: 13px 0; } } }
  /* end media min width 1200 pixels */ }
