/* ----- About page ------------------- */

.slider-about-slide {
  margin: 15px;
  &:nth-child(n+2) {
    display: none; }
  &.slick-slide {
    display: block; } }

.slider-about {
  .slick-list {
    margin: 0 -15px; } }


.about-box__picture {
  box-shadow: 0 3px 15px rgba(0, 0, 0, 0.38);
  background-color: #27213f;
  max-width: 350px;
  margin: 0 auto 20px; }

.about-info__subtitle {
  color: $cl-light;
  font-size: 18px;
  font-weight: 500;
  letter-spacing: 0.18px;
  line-height: 1.2;
  margin-bottom: 15px;
  position: relative;
  padding-left: 60px;
  &:before {
    content: ' ';
    position: absolute;
    bottom: 5px;
    left: 0;
    width: 28px;
    height: 3px;
    background-color: $cl-light;
    background-image: $grad1; } }

.about-info {
  .title-site_mega {
    background-image: $grad2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: #cd14a8;
    font-weight: 800;
    letter-spacing: 1.8px; } }

.about-txt-img {
  max-width: 350px;
  margin: 0 auto 20px;
  text-align: center; }

.about-map {
  max-width: 450px;
  margin: 0 auto 20px;
  text-align: center; }

.skills-card-list {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
  margin-left: -5px;
  margin-right: -5px;

  &__item {
    min-width: 50%;
    max-width: 50%;
    padding-left: 5px;
    padding-right: 5px;
    margin-bottom: 10px; } }

.skills-card {
  position: relative;
  padding: 40px 0;
  text-align: center;
  z-index: 1;
  &:before {
    content: ' ';
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
    background: url(../img/skills1.png) center center no-repeat;
    width: 200px;
    height: 200px;
    background-size: contain; }
  &_two {
    &:before {
      background-image: url(../img/skills2.png); } }
  &_three {
    &:before {
      background-image: url(../img/skills3.png); } }
  &_four {
    &:before {
      background-image: url(../img/skills4.png); } }
  &__value {
    background-image: linear-gradient(to top, #655f7d 0%, $cl-light 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: $cl-light;
    font-size: 32px;
    font-weight: 700;
    line-height: 1;
    letter-spacing: 1.2px;
    position: relative;
    &:after {
      content: attr(data-value);
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-48%, -50%);
      z-index: -1;
      color: #282240;
      background: none;
      -webkit-text-fill-color: #282240;
      -webkit-background-clip: content-box;
      background-clip: content-box; } }
  &__txt {
    color: $cl-light;
    font-size: 12px;
    font-weight: 500;
    line-height: 16px;
    letter-spacing: 0.5px; } }

.brand-img {
  text-align: center;
  img {
    display: inline-block; } }

.slider-flex {
  &_center {
    .slick-track {
      align-items: center; } }
  .slick-slide {
    float: none;
    height: auto;
    flex-shrink: 0; }
  .slick-track {
    display: flex;
    justify-content: flex-start; } }

.slider-brand {
  .slider-brand-slide {
    margin: 15px;
    &:nth-child(n+2) {
      display: none; }
    &.slick-slide {
      display: block; } }
  .slick-list {
    margin-left: -15px;
    margin-right: -15px; } }

.slider-brand-wrapp {
  .slider-nav {
    margin-top: 50px; } }

.planet-img {
  position: relative;
  z-index: 1;
  display: inline-block;
  .icon {
    position: absolute;
    top: 140px;
    left: 140px;
    z-index: 1; } }

.skills-price-list {
  margin-top: 50px;
  &__item {
    margin-bottom: 40px; } }

.skills-price {
  text-align: center;
  &__txt {
    font-size: 20px;
    font-weight: 600;
    line-height: 22px;
    letter-spacing: 0.5px;
    margin-bottom: 10px;
    position: relative;
    z-index: 2; } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .about-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__picture {
      order: 1;
      min-width: 47%;
      max-width: 47%;
      margin: 0;
      height: 300px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
    &__content {
      min-width: 50%;
      max-width: 50%;
      padding: 50px 0 30px; } }

  .about-container__nav {
    text-align: left;
    padding-left: 5px;
    margin-top: -45px; }

  .about-container {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      width: 100%;
      height: 100%;
      background: url(../img/grid-mask-about.png) center top no-repeat;
      background-size: contain;
      z-index: -1; } }

  .skills-card-list {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -10px;
    margin-right: -10px;
    &__item {
      min-width: 25%;
      max-width: 25%;
      padding-left: 10px;
      padding-right: 10px;
      margin-bottom: 20px; } }

  .skills-card {
    padding: 60px 0;
    &:before {
      width: 260px;
      height: 260px; } }

  .grid-cap {
    display: flex;
    justify-content: space-between;
    &__picture {
      order: 1;
      flex-shrink: 0;
      min-width: 330px;
      max-width: 330px; }
    &__content {
      flex-grow: 1; } }

  .skills-price-list {
    display: flex;
    justify-content: space-between;
    align-items: center; }

  .skills-price__value_sm {
    .skills-card {
      width: 120px;
      margin-left: auto;
      margin-right: auto;
      padding: 35px 0;
      &:before {
        width: 183px;
        height: 183px; }
      &__value {
        font-size: 24px; } } }

  .skills-price__value_md {
    .skills-card {
      width: 210px;
      margin-left: auto;
      margin-right: auto;
      padding: 65px 0;
      &:before {
        width: 300px;
        height: 300px; }
      &__value {
        font-size: 42px; } } }

  .skills-price__value_lg {
    .skills-card {
      width: 250px;
      margin-left: auto;
      margin-right: auto;
      padding: 85px 0;
      &:before {
        width: 380px;
        height: 380px; }
      &__value {
        font-size: 50px; } } }

  .skills-price-list {
    margin: 50px -15px 0;
    padding: 0 15px; }
  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .about-box {
    display: flex;
    justify-content: space-between;
    align-items: center;
    &__picture {
      order: 1;
      min-width: 47%;
      max-width: 47%;
      margin: 0;
      height: 450px;
      img {
        width: 100%;
        height: 100%;
        object-fit: cover; } }
    &__content {
      min-width: 50%;
      max-width: 50%;
      padding: 50px 0 30px; } }

  .about-container__nav {
    text-align: left;
    padding-left: 5px;
    margin-top: -30px; }

  .about-info {
    .title-site_mega {
      font-size: 72px; } }
  .about-box {
    align-items: flex-start; }
  .about-box__content {
    min-width: 40%;
    max-width: 40%;
    padding-top: 0;
    padding-bottom: 45px; }
  .about-box__picture {
    max-width: 60%;
    min-width: 60%; }
  .slider-about-slide {
    margin-top: 30px; }
  .about-info .title-site_mega {
    margin-top: -10px;
    margin-bottom: 28px; }
  .about-info__subtitle {
    margin-bottom: 32px; }
  .about-container__nav {
    margin-top: -48px;
    padding-left: 10px; }

  .skills-card {
    position: relative;
    padding: 40px 0;
    text-align: center;
    z-index: 1;
    &__value {
      font-size: 48px;
      line-height: 1; }
    &__txt {
      font-size: 20px;
      font-weight: 600;
      line-height: 22px; } }

  .skills-card {
    padding: 110px 0;
    &:before {
      width: 426px;
      height: 426px; } }

  .about-map {
    max-width: none;
    margin-bottom: 60px; }
  .about-txt-img {
    max-width: none;
    margin-bottom: 50px; }

  .slider-brand-wrapp {
    margin-top: 80px;
    .slider-nav {
      margin-top: 100px; } }

  .skills-price {
    display: flex;
    justify-content: flex-start;
    align-items: center; }

  .skills-price__txt {
    margin-top: 10px; }

  .skills-price-list {
    margin: 50px -15px 0 0; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .about-box__content {
    min-width: 29%;
    max-width: 29%; }
  .about-box__picture {
    max-width: 66%;
    min-width: 66%; }
  .about-container:before {
    right: auto;
    left: 50%;
    margin-left: -630px;
    width: 1323px;
    height: 792px;
    top: -55px; }

  .slider-project-wrapp_price-wrapp .slider-nav {
    margin-top: -120px; }
  /* end media min width 1200 pixels */ }

/* ----- END About page --------------- */
