/* ----- Skills points ------------------- */

.skills-point {
  text-align: center;
  margin-bottom: 40px;
  &__icon {
    max-width: 198px;
    min-height: 175px;
    line-height: 165px;
    background: url(../img/icons/icon-box2.png) center top no-repeat;
    opacity: 1;
    text-align: center;
    margin: 0 auto;
    &_wrapp {
      line-height: 175px;
      .icon {
        position: relative;
        left: -3px; }
      &2 {
        line-height: 170px; } } }
  &__tt {
    text-shadow: 0 21px 43px rgba(31, 50, 77, 0.43);
    color: #f9f5ff;
    font-size: 18px;
    font-weight: 700;
    line-height: 20px;
    font-size: 16px;
    line-height: 18px;
    letter-spacing: 1.78px;
    margin-bottom: 14px; }
  &__txt {
    color: $cl-txt2;
    font-size: 12px;
    line-height: 14px;
    letter-spacing: 0.35px; } }



/* Min width 480 pixels */
@media only screen and (min-width: 480px) {
  .skills-points {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-left: -1%;
    margin-right: -1%;
    .skills-point {
      flex: 0 0 48%;
      max-width: 48%;
      margin-left: 1%;
      margin-right: 1%; } }
  /* end media min width 480 pixels */ }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .skills-points {
    .skills-point {
      flex: 0 0 31.3%;
      max-width: 31.3%;
      margin-bottom: 20px;
      margin-top: 40px; } }

  .skills-point {
    &__tt {
      font-size: 18px;
      line-height: 20px; }

    &:nth-child(3n - 1) {
      &:before {
        top: -30px;
        background: url(../img/line1.png) right top no-repeat; } }

    &:nth-child(3n) {
      &:before {
        top: -33px;
        background: url(../img/line2.png) right top no-repeat; } }

    &:nth-child(3n - 1), &:nth-child(3n) {
      position: relative;
      z-index: 1;
      &:before {
        content: ' ';
        position: absolute;
        right: 100%;
        margin-right: -50%;
        width: 107%;
        background-size: 100% 100%;
        height: 50px;
        z-index: -1; } } }

  .skills-points_four-col {
    .skills-point {
      flex: 0 0 48%;
      max-width: 48%;
      margin-bottom: 20px;
      margin-top: 40px;

      &:nth-child(3n - 1), &:nth-child(3n) {
        position: static;
        &:before {
          display: none; } }

      &:nth-child(4n-2) {
        &:before {
          top: -33px;
          background: url(../img/line2.png) right top no-repeat; } }

      &:nth-child(4n) {
        &:before {
          top: -30px;
          background: url(../img/line1.png) right top no-repeat; } }

      &:nth-child(4n-2), &:nth-child(4n) {
        position: relative;
        z-index: 1;
        &:before {
          content: ' ';
          display: block;
          position: absolute;
          right: 100%;
          margin-right: -50%;
          width: 104%;
          background-size: 100% 100%;
          height: 50px;
          z-index: -1; } } } }

  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .skills-point {
    &__tt {
      font-size: 20px;
      line-height: 24px;
      min-height: 48px; }
    &__txt {
      font-size: 14px;
      line-height: 18px; }
    &__icon {
      min-height: 195px; }
    &__tt {
      margin-bottom: 18px; } }
  /* end media min width 768 pixels */ }

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .skills-point {
    &__tt {
      font-size: 26px;
      line-height: 30px;
      min-height: 60px; }
    &__txt {
      font-size: 14px;
      line-height: 20px; } }

  .skills-points .skills-point {
    margin-top: 90px; }

  .skills-point {

    &:nth-child(3n) {
      &:before {
        top: -33px; } }

    &:nth-child(3n - 1), &:nth-child(3n) {
      &:before {
        top: -58px;
        height: 84px; } } }

  .skills-points {
    margin-left: 0;
    margin-right: 0;
    .skills-point {
      flex: 0 0 24.3%;
      max-width: 24.3%;
      margin-left: 4.5%;
      margin-right: 4.5%; } }

  .skills-point:nth-child(3n - 1):before, .skills-point:nth-child(3n):before {
    width: 138%; }
  .skills-point:nth-child(3n):before {
    top: -65px; }

  .skills-points_four-col {
    margin-left: -1%;
    margin-right: -1%;
    .skills-point {
      flex: 0 0 23%;
      max-width: 23%;
      margin-left: 1%;
      margin-right: 1%;
      &__tt {
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        letter-spacing: 1.37px; }
      &__txt {
        font-size: 14px;
        line-height: 20px;
        color: $cl-txt2; }

      &:nth-child(4n-2), &:nth-child(4n-1), &:nth-child(4n) {
        position: relative;
        z-index: 1;
        &:before {
          display: block;
          width: 109.5%;
          height: 84px;
          top: -58px; } }

      &:nth-child(4n) {
        &:before {
          top: -50px;
          background: url(../img/line3.png) right top no-repeat; } }

      &:nth-child(4n-1) {
        &:before {
          top: -56px;
          background: url(../img/line2.png) right top no-repeat; } }

      &:nth-child(4n-2) {
        &:before {
          top: -50px;
          background: url(../img/line1.png) right top no-repeat; } }

      &:nth-child(4n-2), &:nth-child(4n-1), &:nth-child(4n) {
        &:before {
          background-size: 100% 100%; } } } }
  /* end media min width 1000 pixels */ }


