/* ----- Typography -------------------- */
* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

body {
  width: 100%;
  margin: 0;
  padding: 0;
  font: $site-font-style $site-font-weight $body-text-size $site-font-family;
  color: $site-text-color;
  background: $site-fill-color;
  letter-spacing: 0.35px; }

.bg-page {
  position: relative;
  &:after {
    content: ' ';
    z-index: -2;
    position: absolute;
    bottom: -196px;
    top: 0;
    left: 0;
    right: 0;

    background: $site-fill-color url(../img/bg-page-clean-mob.jpg) center top no-repeat;
    background-size: cover; } }

.title-site, h1, h2, h3, h4, h5, h6 {
  display: block;
  margin: 0 0 15px;
  padding: 0;
  font-family: $title-font-weight;
  font-weight: $title-font-family;
  line-height: $title-line-height;
  color: $site-title-color; }

h1, .title-site_h1 {
  font-size: $site-h1-size-mobile;
  margin-bottom: 20px; }

h2, .title-site_h2 {
  font-size: $site-h2-size-mobile;
  margin-bottom: 20px; }

h3, .title-site_h3 {
  font-size: $site-h3-size-mobile; }

h4, .title-site_h4 {
  font-size: $site-h4-size-mobile; }

h5, .title-site_h5 {
  font-size: $site-h5-size-mobile;
  line-height: 1.3em; }

h6, .title-site_h6 {
  font-size: $site-h6-size-mobile;
  line-height: 1.3em; }

.title-site_bold {
  font-weight: 700; }

ul, ol {
  list-style-type: none;
  margin: 0;
  padding: 0; }

li {
  margin: 0;
  padding: 0; }

p {
  margin: 0 0 20px; }

.text-bl {
  ul {
    margin-bottom: 20px;
    list-style-type: disc;
    padding-left: 15px; }

  ol {
    margin-bottom: 20px;
    list-style-type: decimal;
    padding-left: 15px; }

  li {
    margin-bottom: 10px; } }

.text_center {
  text-align: center; }

.text_left {
  text-align: left; }

.text_right {
  text-align: right; }

.text_uppercase {
  text-transform: uppercase; }

.title-site_font-base {
  font-family: $site-font-family, Arial, sans-serif; }


img {
  vertical-align: middle;
  max-width: 100%; }

a {
  color: $color-link;
  text-decoration: underline;

  &:hover {
    text-decoration: none;
    color: $color-h-link; } }

.list-dots {
  -moz-column-count: 2;
  -webkit-column-count: 2;
  column-count: 2;
  -moz-column-gap: 10px;
  -webkit-column-gap: 10px;
  column-gap: 10px;
  margin-bottom: 20px;
  li {
    vertical-align: top;
    position: relative;
    padding-left: 15px;
    margin: 0 0 8px;
    &:before {
      content: ' ';
      position: absolute;
      top: 5px;
      left: 0;
      width: 3px;
      height: 3px;
      background-image: $grad1;
      background-color: #fff;
      border-radius: 50%; } } }

.cl-light {
  color: $cl-light; }

.s-cap {
  text-align: center;
  margin-bottom: 40px;
  &_sm {
    margin-bottom: 20px; }
  &__tt {
    margin-bottom: 20px;
    margin-left: -15px;
    margin-right: -15px;
    overflow: hidden;
    .title-site, h1, h2, h3, h4, h5, h6 {
      margin-bottom: 0;
      display: inline-block;
      position: relative;
      &:before, &:after {
        content: ' ';
        position: absolute;
        top: 50%;
        width: 127px;
        height: 2px;
        margin-top: -1px; }
      &:after {
        background-image: linear-gradient(to left, #292242 0%, #a890cd 100%);
        left: 100%;
        margin-left: 20px; }
      &:before {
        background-image: linear-gradient(to right, #292242 0%, #a890cd 100%);
        right: 100%;
        margin-right: 20px; } }
    h2, .title-site_h2 {
      font-family: $site-font-family;
      font-weight: 600; } }

  &__txt {
    color: $cl-txt;
    font-size: 14px;
    line-height: 16px;
    letter-spacing: 0.45px;
    &_max-width {
      margin: 0 auto;
      max-width: 570px; } } }

.s-cap_left {
  text-align: left;
  .s-cap__tt {
    margin-left: 0;
    &:before {
      display: none; } } }

.s-cap_mob-sm {
  margin-bottom: 20px;
  .s-cap__tt {
    margin-bottom: 11px; } }

.s-cap__tt_gradient, .s-cap__tt_gradient-second {
  .title-site, h1, h2, h3, h4, h5, h6 {
    background-image: linear-gradient(to top, #84533d 0%, #e7c2a6 100%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text; } }

.s-cap__tt_gradient-second {
  .title-site, h1, h2, h3, h4, h5, h6 {
    background-image: $grad2; } }

.s-cap__tt_bold {
  .title-site, h1, h2, h3, h4, h5, h6 {
    font-weight: 700; } }

.s-cap__subtt {
  color: $cl-light;
  font-weight: 600;
  font-size: 16px;
  line-height: 22px;
  letter-spacing: 0.5px;
  margin-bottom: 15px; }

.tt-grid {
  position: relative;
  z-index: 1;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 -15px 50px;
  padding: 0 15px 30px;
  &__txt {
    flex-grow: 1; }
  &__value {
    background-image: $grad2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: #cb78e0;
    font-family: $site-font-family;
    font-size: 60px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 2px;
    flex-shrink: 0;
    margin-left: 10px; }
  .title-site, h1, h2, h3, h4, h5, h6 {
    margin-bottom: 0; }
  &:before {
    content: ' ';
    position: absolute;
    left: 25px;
    right: 0;
    bottom: 0;
    background-color: $cl-border;
    height: 1px; }
  &:after {
    content: ' ';
    position: absolute;
    left: 0;
    bottom: -28px;
    width: 57px;
    height: 57px;
    background: url(../img/sprite.png) -107px -226px no-repeat; } }
.tt-grid_wrapp.tt-grid {
  .tt-grid__txt {
    order: 1;
    text-align: right; }
  .tt-grid__value {
    margin-left: 0;
    margin-right: 10px; }
  &:before {
    left: 0;
    right: 25px; }
  &:after {
    left: auto;
    right: 0; } }

.s-cap_left {
  .s-cap__txt_max-width {
    margin-left: 0;
    max-width: 720px; } }
/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .tt-grid {
    padding-left: 70px;
    padding-right: 70px;
    margin-bottom: 65px; }
  /* end media min width 640 pixels */ }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  body {
    font-size: 14px;
    line-height: 20px; }
  h1, .title-site_h1 {
    font-size: $site-h1-size; }

  h2, .title-site_h2 {
    font-size: $site-h2-size; }

  h3, .title-site_h3 {
    font-size: $site-h3-size; }

  h4, .title-site_h4 {
    font-size: $site-h4-size; }

  h5, .title-site_h5 {
    font-size: $site-h5-size; }

  h6, .title-site_h6 {
    font-size: $site-h6-size; }

  .text-bl-grid {
    display: flex;
    justify-content: space-between;

    &__half-col {
      width: 48%; } }

  .bg-page {
    &:after {
      background-image: url(../img/bg-page-clean-tabl.jpg);
      bottom: -150px; } }

  .s-cap {
    &_sm {
      margin-bottom: 40px; }
    &_lg {
      margin-bottom: 80px; }
    &__tt {
      margin-bottom: 21px;
      .title-site, h1, h2, h3, h4 {
        &:after {
          margin-left: 30px; }
        &:before {
          margin-right: 30px; } } }
    &__txt {
      font-size: 16px;
      line-height: 20px; } }


  .s-cap__subtt {
    font-size: 20px;
    line-height: 26px; }

  .list-dots {
    -moz-column-gap: 60px;
    -webkit-column-gap: 60px;
    column-gap: 60px; }

  .tt-grid__value {
    font-size: 72px;
    position: relative;
    bottom: -7px; }

  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .s-cap {
    &__tt {
      .title-site, h1, h2, h3, h4 {
        &:after {
          margin-left: 45px; }
        &:before {
          margin-right: 45px; } } }
    &__txt {
      font-size: 18px;
      line-height: 30px;
      &_sm-line-height {
        line-height: 24px; } } }

  .s-cap_mob-sm .s-cap__tt {
    margin-bottom: 20px; }
  .s-cap_mob-sm {
    margin-bottom: 40px; }

  .bg-page {
    &:after {
      bottom: -130px; } }

  .title-site_h1_big {
    font-size: 48px; }

  .tt-grid {
    position: relative;
    z-index: 1;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    margin: 0 0 80px;
    padding: 0 70px 30px; }
  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .bg-page {
    &:after {
      background-image: url(../img/bg-page-clean.jpg);
      background-repeat: no-repeat;
      background-position: center top;
      background-size: cover; } }

  #middle {
    position: relative;
    overflow: hidden;
    &:after {
      z-index: -1;
      content: ' ';
      position: absolute;
      top: 1800px;
      left: 0;
      right: 0;
      height: 4900px;
      background: url(../img/bg-page-stars.png) center top no-repeat;
      background-size: contain; } }

  /* end media min width 1200 pixels */ }

/* ----- END Typography ---------------- */
