/* ----- Animate ------------------- */

/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .anim-hidden {
    opacity: 0; }

  .anim-visible {
    opacity: 1; }

  .animated {
    -webkit-animation-duration: 1s;
    animation-duration: 1s;
    -webkit-animation-fill-mode: both;
    animation-fill-mode: both;

    &.infinite {
      -webkit-animation-iteration-count: infinite;
      animation-iteration-count: infinite; }

    &.delay-halfs {
      -webkit-animation-delay: 0.5s;
      animation-delay: 0.5s; }

    &.delay-300ms {
      -webkit-animation-delay: 300ms;
      animation-delay: 300ms; }

    &.delay-600ms {
      -webkit-animation-delay: 600ms;
      animation-delay: 600ms; }

    &.delay-900ms {
      -webkit-animation-delay: 900ms;
      animation-delay: 900ms; }

    &.delay-1200ms {
      -webkit-animation-delay: 1200ms;
      animation-delay: 1200ms; }

    &.delay-1s {
      -webkit-animation-delay: 1s;
      animation-delay: 1s; }

    &.delay-2s {
      -webkit-animation-delay: 2s;
      animation-delay: 2s; }

    &.delay-3s {
      -webkit-animation-delay: 3s;
      animation-delay: 3s; }

    &.delay-4s {
      -webkit-animation-delay: 4s;
      animation-delay: 4s; }

    &.delay-5s {
      -webkit-animation-delay: 5s;
      animation-delay: 5s; }

    &.fast {
      -webkit-animation-duration: 800ms;
      animation-duration: 800ms; }

    &.faster {
      -webkit-animation-duration: 500ms;
      animation-duration: 500ms; }

    &.slow {
      -webkit-animation-duration: 2s;
      animation-duration: 2s; }

    &.slower {
      -webkit-animation-duration: 3s;
      animation-duration: 3s; } }

  // show-animation bounce-in-left
  // show-animation bounce-in-right
  // show-animation fadeInUp
  // delay-300ms

  .bounce-in-left.animated {
    -webkit-animation-name: bounceInLeft;
    animation-name: bounceInLeft; }

  .bounce-in-right.animated {
    -webkit-animation-name: bounceInRight;
    animation-name: bounceInRight; }

  .fadeInLeft.animated {
    -webkit-animation-name: fadeInLeft;
    animation-name: fadeInLeft; }

  .fadeInRight.animated {
    -webkit-animation-name: fadeInRight;
    animation-name: fadeInRight; }

  .fadeInUp.animated {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp; }

  .zoomIn.animated {
    -webkit-animation-name: zoomIn;
    animation-name: zoomIn; }

  .pulse.animated {
    -webkit-animation-name: pulse;
    animation-name: pulse; }

  .review-box__value {
    .animated.fadeInLeft {
      -webkit-animation-name: fadeInLeftSt;
      animation-name: fadeInLeftSt; } }

  .about-card.animated {
    -webkit-animation-name: fadeInUp;
    animation-name: fadeInUp; }

  .fadeInDown.animated {
    -webkit-animation-name: fadeInDown;
    animation-name: fadeInDown; }

  .banner-comet {
    animation: {
      name: fadeInDownComet;
      duration: 1s;
      fill-mode: both;
      delay: 2s; }
    &_one {
      animation: {
        delay: 2s; } }
    &_two {
      animation: {
        delay: 3.6s; } }
    &_three {
      animation: {
        delay: 4.2s; } } }

  .banner__img:after {
    animation: {
      name: rotate;
      iteration-count: infinite;
      fill-mode: both;
      duration: 100s;
      timing-function: linear; } }

  #middle:after {
    animation: {
      name: starsmain;
      iteration-count: infinite;
      fill-mode: both;
      duration: 15s;
      timing-function: linear; } }

  .banner-footer-line {
    position: relative;
    z-index: 2; }

  .banner-footer-skills {
    z-index: 3;
    position: relative; }

  .banner-footer-line {
    animation: {
      name: fadeInUp;
      duration: 1s;
      fill-mode: both; } }

  .banner__content {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      fill-mode: both; } }

  .banner__content p {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      delay: 0.3s;
      fill-mode: both; } }

  .animated.section-st_bg-satellite:after {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      delay: 1s;
      fill-mode: both; } }

  .banner__content p:nth-child(2) {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      delay: 0.6s;
      fill-mode: both; } }

  .banner__content .btn {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      delay: 0.9s;
      fill-mode: both; } }

  .banner-footer-skills__item_left.animated {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      fill-mode: both; } }

  .banner-footer-skills__item_right.animated {
    animation: {
      name: bounceInRight;
      delay: 0.3s;
      duration: 1s;
      fill-mode: both; } }

  .animated.service-mask-grid:before {
    animation: {
      name: bounceInRight;
      duration: 1s;
      fill-mode: both; } }

  .animated.section-st_bg-ufo:before {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      delay: 1s;
      fill-mode: both; } }

  .animated.section-st_bg-hole:after {
    animation: {
      name: bounceInRight;
      duration: 1s;
      delay: 1s;
      fill-mode: both; } }

  .animated.service-mask-grid:after {
    animation: {
      name: bounceInLeft;
      duration: 1s;
      fill-mode: both; } }

  .banner-footer-skills__item_center.animated {
    animation: {
      name: fadeInUp;
      delay: 0.6s;
      duration: 1s;
      fill-mode: both; } }

  .banner-footer-skills__img.animated {
    animation: {
      name: zoomIn;
      duration: 1s;
      fill-mode: both; } }

  .banner-footer__social:nth-child(1) {
    animation: {
      name: fadeInLeft;
      duration: 1s;
      fill-mode: both;
      delay: 1s; } }

  .banner-footer__social:nth-child(2) {
    animation: {
      name: fadeInRight;
      duration: 1s;
      delay: 2s;
      fill-mode: both; } }

  .s-cap__tt.animated {
    animation: {
      name: fadeInUp;
      duration: 1s;
      fill-mode: both; } }

  .s-cap__txt.animated {
    animation: {
      name: fadeInLeft;
      duration: 1s;
      delay: 0.6s;
      fill-mode: both; } }

  .main-card {
    @for $i from 1 through 4 {
      &:nth-child(#{$i}) {
        -webkit-animation-delay: (150ms * $i) - 300;
        animation-delay: (150ms * $i) - 300; } } }

  @keyframes fadeInDownComet {
    from {
      opacity: 0;
      -webkit-transform: translate3d(80px, -100%, 0);
      transform: translate3d(80px, -100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes starsmain {
    from {
      top: 1800px; }
    50% {
      top: 1900px; }
    to {
      top: 1800px; } }

  @-webkit-keyframes pulse {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }

    50% {
      -webkit-transform: scale3d(1.05, 1.05, 1.05);
      transform: scale3d(1.05, 1.05, 1.05); }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); } }

  @keyframes pulse {
    from {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); }

    50% {
      -webkit-transform: scale3d(1.05, 1.05, 1.05);
      transform: scale3d(1.05, 1.05, 1.05); }

    to {
      -webkit-transform: scale3d(1, 1, 1);
      transform: scale3d(1, 1, 1); } }

  @-webkit-keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInRight {
    from {
      opacity: 0;
      -webkit-transform: translate3d(100%, 0, 0);
      transform: translate3d(100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInLeft {
    from {
      opacity: 0;
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes fadeInLeftSt {
    from {
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInLeftSt {
    from {
      -webkit-transform: translate3d(-100%, 0, 0);
      transform: translate3d(-100%, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes bounceInRight {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    from {
      opacity: 0;
      -webkit-transform: translate3d(3000px, 0, 0);
      transform: translate3d(3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(-25px, 0, 0);
      transform: translate3d(-25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(10px, 0, 0);
      transform: translate3d(10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(-5px, 0, 0);
      transform: translate3d(-5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes bounceInRight {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    from {
      opacity: 0;
      -webkit-transform: translate3d(3000px, 0, 0);
      transform: translate3d(3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(-25px, 0, 0);
      transform: translate3d(-25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(10px, 0, 0);
      transform: translate3d(10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(-5px, 0, 0);
      transform: translate3d(-5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes bounceInLeft {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes bounceInLeft {
    from,
    60%,
    75%,
    90%,
    to {
      -webkit-animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
      animation-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1); }

    0% {
      opacity: 0;
      -webkit-transform: translate3d(-3000px, 0, 0);
      transform: translate3d(-3000px, 0, 0); }

    60% {
      opacity: 1;
      -webkit-transform: translate3d(25px, 0, 0);
      transform: translate3d(25px, 0, 0); }

    75% {
      -webkit-transform: translate3d(-10px, 0, 0);
      transform: translate3d(-10px, 0, 0); }

    90% {
      -webkit-transform: translate3d(5px, 0, 0);
      transform: translate3d(5px, 0, 0); }

    to {
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes rotate {
    0% {
      transform: rotate(0); }

    25% {
      transform: rotate(45deg); }

    50% {
      transform: rotate(0deg); }

    75% {
      transform: rotate(-45deg); }

    100% {
      transform: rotate(0); } }

  @keyframes rotate-back {
    from {
      transform: rotate(0); }

    to {
      transform: rotate(-360deg); } }


  @-webkit-keyframes zoomIn {
    from {
      opacity: 0;
      -webkit-transform: scale3d(0.3, 0.3, 0.3);
      transform: scale3d(0.3, 0.3, 0.3); }

    50% {
      opacity: 1; } }

  @keyframes zoomIn {
    from {
      opacity: 0;
      -webkit-transform: scale3d(0.3, 0.3, 0.3);
      transform: scale3d(0.3, 0.3, 0.3); }

    50% {
      opacity: 1; } }

  @-webkit-keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInUp {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, 100%, 0);
      transform: translate3d(0, 100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }


  @-webkit-keyframes fadeInDown {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }

  @keyframes fadeInDown {
    from {
      opacity: 0;
      -webkit-transform: translate3d(0, -100%, 0);
      transform: translate3d(0, -100%, 0); }

    to {
      opacity: 1;
      -webkit-transform: translate3d(0, 0, 0);
      transform: translate3d(0, 0, 0); } }
  /* end media min width 1000 pixels */ }

/* ----- END Animate --------------- */
