/* ----- Navigation -------------------- */
/* -- Bread crumbs ------------------ */
.bread-crumbs {
  padding: 0;
  list-style-type: none;
  display: none;

  @include tabl {
    display: block; }

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 0;
    color: #aab8d5;
    font-size: 14px;
    line-height: 18px;
    letter-spacing: 0.35px;
    a {
      text-decoration: none;
      outline: none;
      color: #aab8d5;
      &:hover {
        text-decoration: underline;
        color: lighten(#aab8d5, 10%); } }

    &:before {
      content: '';
      margin: 0 7px 0 5px;
      display: inline-block;
      vertical-align: middle;
      background-image: url(../img/sprite.png);
      background-repeat: no-repeat;
      width: 35px;
      height: 5px;
      background-position: -56px -5px; }

    &:first-child {
      &:before {
        display: none; } } } }


/* -- Pagination -------------------- */
.pagination {
  list-style-type: none;

  li {
    display: inline-block;
    vertical-align: middle;
    margin: 10px 5px; }

  a {
    display: block;
    width: 30px;
    height: 30px;
    border-radius: 2px;
    background-color: #fff;
    line-height: 30px;
    text-align: center;
    text-decoration: none;
    color: #000;
    &:hover {
      background-color: #c1288f;
      color: #fff; } }

  &__prev,
  &__next {
    a {
      width: auto;
      height: auto;
      border-radius: 0;
      background: none;
      color: #fff;

      &:hover {
        background: none;
        color: #f2f2f2; } } }

  &__active {
    a {
      background-color: #c1288f;
      color: #fff; } } }


/* -- Main menu --------------------- */
.menu-open-wrapper-page {
  overflow: hidden;
  position: fixed;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0; }

.burger-menu {
  display: block;
  position: absolute;
  right: 5px;
  top: 17px;
  z-index: 101;
  cursor: pointer;
  padding: 10px; }

.burger-menu-box {
  display: block;
  width: 30px;
  padding: 8px 0; }

.burger-menu-inner {
  display: block;
  position: relative;
  height: 3px;
  background-color: $burger-fill;
  border-radius: 3px;
  transition: transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19);

  &:before,
  &:after {
    content: ' ';
    position: absolute;
    left: 0;
    height: 3px;
    background-color: $burger-fill;
    width: 100%;
    border-radius: 3px;
    transition-timing-function: ease;
    transition-duration: .15s;
    transition-property: transform; }

  &:before {
    top: -8px;
    transition: top 75ms ease .12s, opacity 75ms ease; }

  &:after {
    bottom: -8px;
    transition: bottom 75ms ease .12s, transform 75ms cubic-bezier(0.55, 0.055, 0.675, 0.19); } }

.menu-burger--opened {
  .burger-menu {
    position: fixed; }

  .burger-menu-inner {
    transition-delay: 0.12s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
    transform: rotate(45deg);

    &:before {
      top: 0;
      transition: top 75ms ease, opacity 75ms ease 0.12s;
      opacity: 0; }

    &:after {
      bottom: 0;
      transition: bottom 75ms ease, transform 75ms cubic-bezier(0.215, 0.61, 0.355, 1) .12s;
      transform: rotate(-90deg); } } }


.menu-wrapper-fixed {
  position: fixed;
  top: 0;
  z-index: 100;
  height: 100%;
  width: 100%;
  left: -100%;
  transition: left 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000); }

.menu-open-wrapper {
  padding: 20px;
  height: 100%;
  overflow: auto;
  -webkit-overflow-scrolling: touch;
  transition: background 0.25s ease-out; }

.menu-burger--opened {
  .menu-wrapper-fixed {
    left: 0; }

  .menu-open-wrapper {
    background-color: rgba($fill-menu, 0.9); }

  .menu-item {
    margin-left: 0;
    @for $i from 1 through 10 {
      &:nth-child(#{$i}) {
        transition-delay: (0.06s * $i) - 0.06s; } } } }

.header-nav {
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  min-height: 100%; }

.menu {
  margin: 0;
  padding: 0;
  list-style: none; }

.menu-item {
  position: relative;
  margin-top: 20px;
  margin-bottom: 20px;
  padding: 0;
  list-style-type: none;
  font: $font-menu-item-mobile;
  color: #fff;
  width: 100%;
  margin-left: -100%;
  transition: all 0.4s cubic-bezier(0.000, 0.995, 0.990, 1.000);

  &_middle {
    display: none; }

  &_active {
    .menu-item__link {
      &:hover {
        border-bottom-color: transparent;
        cursor: default;
        color: #fff; }

      &:after {
        display: block; } } }

  &__link {
    color: #fff;
    text-decoration: none;
    position: relative;
    display: inline-block;
    border-bottom: 2px solid transparent;
    z-index: 1;
    &:hover {
      border-bottom-color: #5088E8;
      color: rgba(#fff, 0.9); }
    &:after {
      content: ' ';
      display: none;
      position: absolute;
      top: 50%;
      right: 0;
      transform: translate(50%, -50%);
      background: url(../img/sprite.png) -5px -5px no-repeat;
      width: 41px;
      height: 41px;
      z-index: -1; } } }

/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .menu-burger--opened {
    .menu-open-wrapper {
      background-color: transparent; } }

  .menu-open-wrapper-page {
    overflow: visible;
    position: relative;
    height: auto;
    width: auto; }

  .menu-wrapper-fixed {
    position: relative;
    left: 0;
    top: 0;
    height: auto;
    transition: none;
    width: 100%; }

  .menu-open-wrapper {
    background: none;
    padding: 0;
    height: auto;
    overflow: visible; }

  .burger-menu {
    display: none; }

  .header-nav {
    display: block; }

  .menu {
    text-align: left;
    border-radius: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: -10px;
    margin-right: -10px; }


  .menu-item {
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0 10px;
    flex-shrink: 1;
    text-align: center;
    font-size: 16px;
    line-height: 20px;
    &_middle.menu-item {
      margin-left: auto;
      margin-right: auto;
      flex-shrink: 2;
      padding-bottom: 20px; }

    &__link {
      display: block;
      padding: 7px 0; } }


  /* end media min width 768 pixels */ }


/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .menu {
    margin-left: 0;
    margin-right: 0;
    &-item {
      font-size: 18px;
      margin: 0 26px; } }

  /* end media min width 1000 pixels */ }

