/* ----- Service ------------------- */

.service-block {
  margin-bottom: 10px;
  position: relative;
  z-index: 1;
  &__tt {
    color: $cl-light;
    font-size: 22px;
    font-weight: 700;
    line-height: 1.1;
    margin-bottom: 14px; }
  p {
    color: $cl-txt2;
    margin-bottom: 20px;
    font-size: 12px;
    line-height: 14px; } }


.service-block-cap {
  margin-bottom: 16px;
  position: relative;
  padding-top: 77px;
  &__img {
    position: absolute;
    top: 0;
    left: -8px;
    z-index: -1;
    max-width: 220px; }
  &__grid {
    .icon {
      vertical-align: bottom; } }
  &__value {
    background-image: $grad2;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    color: #9984f1;
    font-size: 72px;
    font-weight: 800;
    line-height: 1;
    text-transform: uppercase;
    letter-spacing: 10px;
    display: inline-block;
    vertical-align: bottom;
    margin-right: 10px; } }


/* Min width 640 pixels */
@media only screen and (min-width: 640px) {
  .service-block {
    float: left;
    width: 48%;
    margin-bottom: 26px;
    clear: left;
    &:nth-child(2) {
      margin-top: 220px; }
    &:nth-child(2n) {
      float: right;
      clear: right; } }

  .service-blocks {
    max-width: 840px;
    margin-left: auto; }
  .service-mask-grid {
    background: url(../img/grid-mask-service.png) center center no-repeat;
    background-size: auto 90%; }

  .service-mask-grid {
    overflow: hidden;
    position: relative;
    z-index: 1;
    &:before {
      content: ' ';
      position: absolute;
      top: 40px;
      right: 10px;
      width: 116px;
      height: 73px;
      background: url(../img/rocket.png) center center no-repeat;
      background-size: contain; } }

  /* end media min width 640 pixels */ }


/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .service-block {
    margin-bottom: 47px; }

  .service-block:nth-child(2) {
    margin-top: 210px; }

  .service-block {
    p {
      font-size: 14px;
      line-height: 20px; }
    &__tt {
      font-size: 26px;
      line-height: 32px; } }

  /* end media min width 768 pixels */ }



/* Min width 1000 pixels */
@media only screen and (min-width: 1000px) {
  .service-block {
    width: 41%;
    margin-bottom: 0; }
  .service-block-cap {
    padding-top: 175px;
    margin-bottom: 37px; }
  .service-block-cap__img {
    max-width: 390px;
    min-width: 390px;
    text-align: center;
    left: -38px;
    &_two {
      top: 10px; }
    &_three {
      top: 30px; } }
  .service-block-cap__grid .icon {
    margin-bottom: 10px; }
  .service-block-cap__value {
    margin-right: 23px; }
  .service-block__tt {
    margin-bottom: 20px; }
  .service-block p {
    margin-bottom: 40px; }
  .service-block:nth-child(2) {
    margin-top: 310px; }

  .service-mask-grid {
    background-position: left calc(50% + 200px) top calc(50% + 47px);
    background-size: auto; }

  /* end media min width 1000 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .service-mask-grid {
    &:after {
      content: ' ';
      position: absolute;
      top: 50%;
      left: 50%;
      width: 553px;
      height: 835px;
      margin-left: -950px;
      margin-top: -370px;
      background: url(../img/planet-big.png) center center no-repeat;
      z-index: -1; } }

  .service-mask-grid {
    &:before {
      top: 80px;
      right: 10px;
      width: 216px;
      height: 134px; } }

  /* end media min width 1200 pixels */ }


/* Min width 1600 pixels */
@media only screen and (min-width: 1600px) {
  .service-mask-grid {
    &:before {
      right: auto;
      left: 50%;
      margin-left: 580px; } }
  /* end media min width 1600 pixels */ }


/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .service-mask-grid {
    &:after {
      left: 0;
      margin-left: 0; } }
  /* end media min width 1800 pixels */ }


