/* ----- Footer ------------------------ */
.footer {
  padding: 20px 0; }

.f-logo {
  margin-bottom: 30px;
  a {
    display: block;
    outline: none; } }

.f-contact-bl {
  margin-bottom: 20px;
  &__tt {
    color: $cl-light;
    font-size: 24px;
    font-weight: 600;
    line-height: 1.1;
    margin-bottom: 5px; }

  &__txt {
    color: $cl-txt2;
    font-size: 18px;
    letter-spacing: 0.45px;
    line-height: 23px;
    margin-bottom: 10px; } }

.icon_phone {
  width: 19px;
  height: 21px;
  background: url(../img/icons/phone.png) center center no-repeat; }

.icon_mail {
  width: 20px;
  height: 16px;
  background: url(../img/icons/mail.png) center center no-repeat; }

.f-contact-points {
  margin: 30px 0; }

.f-contact-point {
  color: $cl-txt2;
  font-size: 18px;
  line-height: 20px;
  letter-spacing: 0.45px;
  position: relative;
  padding-left: 40px;
  margin-bottom: 15px;
  .icon {
    position: absolute;
    top: 0;
    left: 0;
    &_phone {
      left: 3px; }
    &_mail {
      top: 3px; } }
  a {
    text-decoration: none;
    display: inline-block;
    outline: none;
    color: $cl-txt2;
    &:hover {
      color: darken($cl-txt2, 10%);
      text-decoration: none;
      .icon {
        opacity: 0.7; } } } }

.social_left {
  a {
    &:first-child {
      padding-left: 0; } } }

.f-contact-info {
  margin-bottom: 50px; }

.footer {
  color: $cl-light; }

.f-menu {
  text-align: center;
  li {
    color: $cl-light;
    margin: 20px 0;
    a {
      color: $cl-light;
      text-decoration: none;
      outline: none;
      &:hover {
        text-decoration: none;
        color: darken($cl-light, 20%); } } } }



/* Min width 768 pixels */
@media only screen and (min-width: 768px) {
  .f-contact-grid {
    display: flex;
    justify-content: space-between;
    &__info {
      flex: 0 0 38%;
      max-width: 38%; }
    &__form {
      flex: 0 0 60%;
      max-width: 60%; } }

  .footer {
    padding: 35px 0; }

  .f-menu {
    display: flex;
    justify-content: space-between;
    text-align: center;
    margin-left: -10px;
    margin-right: -10px;
    li {
      color: $cl-light;
      margin: 20px 10px; } }

  /* end media min width 768 pixels */ }


/* Min width 1200 pixels */
@media only screen and (min-width: 1200px) {
  .section-st_bg-satellite {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: 0;
      left: 20px;
      width: 200px;
      height: 160px;
      background: url(../img/satellite.png) center center no-repeat;
      background-size: contain;
      z-index: -1; }

    &_second {
      overflow: hidden;
      &:after {
        display: none; } } }
  /* end media min width 1200 pixels */ }


/* Min width 1440 pixels */
@media only screen and (min-width: 1440px) {
  .section-st_bg-satellite {
    position: relative;
    &:after {
      width: 281px;
      height: 160px; }

    &_second {
      overflow: hidden;
      position: relative;
      &:after {
        display: block;
        width: 100px;
        height: 100px;
        content: ' ';
        position: absolute;
        top: 0;
        left: 20px;
        background: url(../img/satellite.png) center center no-repeat;
        background-size: contain;
        z-index: -1; } } }
  .section-st_bg-minion_second {
    position: relative;
    overflow: hidden;
    &:before {
      content: ' ';
      position: absolute;
      top: 50%;
      right: 20px;
      width: 100px;
      height: 100px;
      background: url(../img/minion.png) center center no-repeat;
      background-size: contain;
      transform: rotate(19deg); } }
  .section-st_bg-ufo {
    position: relative;
    &:after {
      content: ' ';
      position: absolute;
      top: 0;
      right: 20px;
      width: 150px;
      height: 216px;
      background: url(../img/ufo.png) center center no-repeat;
      background-size: contain;
      z-index: -1; } }
  .section-st_bg-hole {
    position: relative;
    &:before {
      content: ' ';
      position: absolute;
      bottom: 0;
      left: 20px;
      width: 150px;
      height: 216px;
      background: url(../img/hole.png) center center no-repeat;
      background-size: contain;
      z-index: -1; } }
  /* end media min width 1440 pixels */ }

/* Min width 1800 pixels */
@media only screen and (min-width: 1800px) {
  .section-st_bg-satellite {
    position: relative;
    &:after {
      left: 50%;
      margin-left: -890px; } }
  .section-st_bg-hole {
    &:before {
      left: 50%;
      margin-left: -900px;
      width: 208px;
      height: 216px;
      bottom: 70px; } }

  .section-st_bg-ufo {
    &:after {
      left: 50%;
      right: auto;
      margin-left: 710px;
      width: 180px;
      height: 100px; } }

  .section-st_bg-satellite {
    &_second {
      &:after {
        width: 204px;
        height: 116px;
        left: 20px; } } }
  .section-st_bg-minion_second {
    &:before {
      width: 99px;
      height: 161px;
      right: 40px; } }
  /* end media min width 1800 pixels */ }


/* ----- END Footer -------------------- */
